<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="chapter_detail_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>챕터 상세 </v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled v-model="editedItem.seq_chapter" label="챕터"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.title" label="title"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-textarea
                        label="긴 줄거리"
                        v-model="editedItem.summary"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      chapter_detail_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: '챕터', value: 'seq_chapter' },
        { text: '제목', value: 'title' },
        { text: '요약', value: 'summary' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetch_chapter_detail();
  },
  computed: {
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'FETCH_CHAPTER_DETAIL',
      'UPDATE_CHAPTER_DETAIL',
    ]),
    editItem (item) {
      this.editedIndex = this.chapter_detail_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      this.UPDATE_CHAPTER_DETAIL(this.editedItem)
        .then(() => {
          this.fetch_chapter_detail();
        })
      this.close()
    },
    fetch_chapter_detail() {
      this.FETCH_CHAPTER_DETAIL(this.seq_title)
        .then((body_data) => {this.chapter_detail_list = body_data.chapter_detail_list});
    }
  }
};
</script>
