<template>
  <v-container>
    <v-layout text-center wrap>
      <v-btn @click="export_file">파일 추출</v-btn>
      <v-file-input label="파일 입력" @change="import_file"></v-file-input>
    </v-layout>
    <v-data-table
        :headers="headers"
        :items="chapter_root_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:item.seq_root="{ item }">
        <div v-for="root_item in item.root_list" :key="root_item.seq_root">
          {{root_item.seq_root}}
        </div>
      </template>
      <template v-slot:item.contents="{ item }">
        <div v-for="root_item in item.root_list" :key="root_item.seq_root">
          {{root_item.contents}}
        </div>
      </template>
      <template v-slot:item.title="{ item }">
        <div v-for="root_item in item.root_list" :key="root_item.seq_root">
          {{root_item.title}}
        </div>
      </template>
      <template v-slot:item.summary="{ item }">
        <div v-for="root_item in item.root_list" :key="root_item.seq_root">
          {{root_item.summary}}
        </div>
      </template>
    </v-data-table>
  </v-container>
</template>

<script src="xlsx.full.min.js"></script>
<script src="js/jhxlsx.js"></script>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
export default {
  name: 'ChapterInfo',
  data() {
    return {
      chapter_root_list: [],
      chapter_root_list_origin: [],
      seq_title: 0,
      headers: [
        { text: '챕터', value: 'seq_chapter' },
        { text: '루트 인덱스', value: 'seq_root', sortable: false },
        { text: '컨텐츠', value: 'contents', sortable: false },
        { text: '타이틀', value: 'title', sortable: false },
        { text: '요약', value: 'summary', sortable: false },
      ],
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title;
    this.fetch_title_root_list();
  },
  computed: {
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_TITLE_ROOT',
      'UPDATE_TITLE_ROOT',
      'UPDATE_TITLE_CHAPTER_ROOT_LIST'
    ]),
    fetch_title_root_list() {
      this.chapter_root_list = [];

      this.$nextTick(() => {
        this.FETCH_TITLE_ROOT(this.seq_title)
        .then((body_data) => {
          let full_chapter_root_list = body_data.chapter_root_list;
          this.chapter_root_list_origin = body_data.chapter_root_list;
          
          let temp_object = {};
          full_chapter_root_list.forEach((item) => {
            let seq_chapter_obj = temp_object[item.seq_chapter];
            if(!seq_chapter_obj) {
              seq_chapter_obj = {}
              seq_chapter_obj.seq_chapter = item.seq_chapter;
              seq_chapter_obj.root_list = [];
              temp_object[item.seq_chapter] = seq_chapter_obj;
            }

            console.log(item);
            seq_chapter_obj.root_list.push({
                seq_root: item.seq_root, 
                contents: item.contents,
                title: item.title,
                summary: item.summary,
              })
          })

          let key_list = Object.keys(temp_object);
          key_list.forEach((item) => {
            this.chapter_root_list.push(temp_object[item])
          })
        });
      })
    },
    close () {
    },
    check_null_value(value, ref, message) {
      if(!value) {
          this.$nextTick(() => ref.focus())
          this.save_error = message;  
          return false;
        }
        return true;
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          console.log(workbook.Sheets[sheetName]);
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_TITLE_CHAPTER_ROOT_LIST({seq_title: this.seq_title, chapter_root_list: json_object})
            .then(() => {
              this.fetch_title_root_list();
            });
        })
      };

      reader.onerror = function(ex) {
          console.log(ex);
      };

      reader.readAsBinaryString(file);
    },
    export_file() {
        var ws = XLSX.utils.json_to_sheet(this.chapter_root_list_origin);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"chapter_root");
        // Jhxlsx.export(json_object, {fileName: "Filename"});
        XLSX.writeFile(wb, `chapter_root_${this.seq_title}.xlsx`);
    },
    onCreate() {

    }
  }
};
</script>
