<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-overlay :value="updating">
      {{updating}}
    </v-overlay>
    <v-container v-if="isLive" >
      <v-alert type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
    </v-container>
      <v-container>
        <v-layout align-left>
          <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>  
          <v-btn v-if="isLive" color="primary" dark class="mb-2" @click="meta_update_live">메타 파일 서버 적용(Live)</v-btn>  
        </v-layout>
      </v-container>
      <v-data-table
        :headers="headers"
        :items="title_purchase_ticket_list"
        sort-by="seq_title"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>타이틀 티켓 코스트</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" >
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="12" sm="4">
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field disabled v-model="editedItem.seq_title" label="타이틀 번호"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.ticket_type" label="메모"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.pay_key" label="구매 키"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.ticket_cost" label="티켓 비용"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12">
                          <v-text-field v-model="editedItem.ticket_qny" label="티켓 개수"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12">
                          <v-textarea v-model="editedItem.ticket_bonus_qny" label="보너스 티켓 개수"></v-textarea>
                        </v-col>
                        <v-col cols="12" sm="12">
                          <v-textarea v-model="editedItem.pay_type" label="지불 타입"></v-textarea>
                        </v-col>
                      </v-row>
                    </v-container>
                </v-col>
              </v-row>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <!--<v-btn color="blue darken-1" text @click="close">Cancel</v-btn>-->
                <!--<v-btn color="blue darken-1" text @click="save">Save</v-btn>-->
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <!-- <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)" >
            edit
          </v-icon>
        </template>
        -->
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
    <v-btn @click="export_file">파일 추출</v-btn>
    <v-file-input label="파일 입력" @change="import_file"></v-file-input>
  </v-container>
</template>

<script>
import XLSX from 'xlsx';
import { mapActions } from 'vuex';
import config from '../../src/config/config';
const dateFormat = require('dateformat');
const {exportPurgeTxt} = require("../util/fileutil");

export default {
  name: 'Banner',
  data() {
    return {
      updating: "",
      dialog: false, 
      title_purchase_ticket_list: [],
      headers: [
        { text: '타이틀 일련번호', value: 'seq_title' },
        { text: '티켓 타입', value: 'ticket_type' },
        { text: '구매 키', value: 'pay_key' },
        { text: '티켓 가격', value: 'ticket_cost' },
        { text: '티켓 개수', value: 'ticket_qny' },
        { text: '보너스 티켓 개수', value: 'ticket_bonus_qny' },
        { text: '구매 방법', value: 'pay_type' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {},
      itemList: [],
    }
  },
  created() {
    this.fetch_title_purchase_ticket();
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_TITLE_PURCHASE_TICKET',
      'UPDATE_TITLE_PURCHASE_TICKET',
      'META_TITLE_SIMPLE_UPDATE',
      'META_TITLE_SIMPLE_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_TITLE_SIMPLE_UPDATE();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      const result = await this.META_TITLE_SIMPLE_UPDATE_LIVE();
      exportPurgeTxt(result.purge_list, `TitleData_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = ""
    },
    fetch_title_purchase_ticket() {
      this.FETCH_TITLE_PURCHASE_TICKET()
      .then((body_data) => {
        this.title_purchase_ticket_list = body_data.title_purchase_ticket_list;
      });
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_TITLE_PURCHASE_TICKET({title_purchase_ticket: json_object})
            .then(() => {
              this.fetch_title_purchase_ticket();
            });
        })
      };

      reader.onerror = function(ex) {
          console.log(ex);
      };

      reader.readAsBinaryString(file);
    },
    export_file() {
        
        var ws = XLSX.utils.json_to_sheet(this.title_purchase_ticket_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"title_purchase_ticket");
        XLSX.writeFile(wb, `TitlePurchaseTicket.xlsx`);
    },
  }
};
</script>
