<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="notice_gms_list"
        sort-by="seq_notice"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>GMS공지</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark class="mb-2" @click="onCreate">New Item</v-btn>
          <v-dialog v-model="dialog">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
                  <v-row>
                    <v-col cols="12" sm="4" md="4">
                    <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field :disabled="!is_edit" v-model="editedItem.seq_notice" label="seq_notice" ref="seq_notice"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.type"
                        :items="typeList"
                        item-text="text"
                        item-value="value"
                        label="공지 유형"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.purpose" label="공지 목적" ref="purpose"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.start_date" label="노출 시간(시작)" ref="start_date"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.end_date" label="노출 시간(종료)" ref="end_date"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.country"
                        :items="countryList"
                        item-text="text"
                        item-value="value"
                        label="적용 타입"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.show_type"
                        :items="showTypeList"
                        item-text="text"
                        item-value="value"
                        label="적용 타입"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.repeat_period_sec" label="반복주기" ref="repeat_period_sec"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.service_age"
                        :items="serviceAgeList"
                        item-text="text"
                        item-value="value"
                        label="서비스 연령"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.market"
                        :items="marketList"
                        item-text="text"
                        item-value="value"
                        label="마켓 리스트"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.app_version" label="앱 버젼" ref="app_version"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.admin_id" label="관리자 아이디" ref="admin_id"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.state"
                        :items="stateList"
                        item-text="text"
                        item-value="value"
                        label="마켓 리스트"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    </v-row>
                    </v-col>
                    <v-col>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            :items="langIdList"
                            item-text="text"
                            item-value="value"
                            label="언어"
                            persistent-hint
                            v-model="add_item.lang_id"
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-textarea
                            label="내용"
                            v-model="add_item.content"
                          ></v-textarea>
                          <v-btn @click="addItem">추가</v-btn>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col>
                        <v-divider></v-divider>
                        <div v-for="(item, index) in editedLangList" :key="index">
                          {{item.lang_id}}
                          {{item.content}}
                          <v-btn @click="deleteLangId(item)">삭제</v-btn>
                        </div>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.country="{ item }">
          {{typeList.find(element => element.value === item.country).text}}
        </template>
        <template v-slot:item.show_type="{ item }">
          {{showTypeList.find(element => element.value === parseInt(item.show_type)).text}}
        </template>
        <template v-slot:item.service_age="{ item }">
          {{serviceAgeList.find(element => element.value === item.service_age).text}}
        </template>
        <template v-slot:item.state="{ item }">
          {{stateList.find(element => element.value === item.state).text}}
        </template>
        <template v-slot:item.market="{ item }">
          {{marketList.find(element => element.value === item.market).text}}
        </template>
        
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
export default {
  name: 'ChapterInfo',
  data() {
    return {
      is_edit: false,
      editedLangList: [],
      add_item: {},
      s3_url: "",
      resource_dialog: false,
      aos_resource: true,
      ios_resource_list: [],
      aos_resource_list: [],
      notice_gms_list: [],
      notice_gms_lang_list: [],
      illust_dialog: false,
      seq_title: 0,
      dialog: false,
      save_error: "",
      langIdList:[
        {value: 1, text: '한국어'},
      ],
      typeList: [
        {value: 1, text: '점검 공지'},
        {value: 2, text: '이벤트 공지'}
      ],
      countryList: [
        {value: 1, text: 'KST'},
        {value: 2, text: 'IP'}
      ],
      showTypeList: [
        {value: 1, text: '반복형'},
        {value: 2, text: '고정형'}
      ],
      serviceAgeList: [
        {value: 0, text: 'ALL'},
        {value: 1, text: '12세'},
        {value: 2, text: '18세'},
      ],
      marketList: [
        {value: 0, text: 'ALL'},
        {value: 1, text: 'apple'},
        {value: 2, text: 'google'},
      ],
      stateList: [
        {value: 1, text: '노출'},
        {value: 2, text: '숨김'},
      ],
      headers: [
        { text: 'seq_notice', value: 'seq_notice' },
        { text: '공지 유형', value: 'type' },
        { text: '공지 목적', value: 'purpose' },
        { text: '노출 시간(시작)', value: 'start_date' },
        { text: '노출 시간(종료)', value: 'end_date' },
        { text: '적용타입(시간)', value: 'country' },
        { text: '노출 방식', value: 'show_type' },
        { text: '반복주기', value: 'repeat_period_sec' },
        { text: '서비스 연령', value: 'service_age' },
        { text: '마켓', value: 'market' },
        { text: '앱 버젼', value: 'app_version' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  
  created() {
    this.seq_title = this.$route.params.seq_title;
    this.fetch_gms_notice();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    check_chapter_list() {
      return chapter_list.length() > 0;
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_GMS_NOTICE',
      'INSERT_GMS_NOTICE',
      'UPDATE_GMS_NOTICE',
      'DELETE_GMS_NOTICE'
    ]),
    deleteLangId(item) {
      const index = this.editedLangList.indexOf(item)
      this.editedLangList.splice(index, 1)
    },
    addItem() {
      this.editedLangList.push(Object.assign({}, this.add_item));
      this.add_item = {}
    },
    change_resource_type(type) {
      this.aos_resource = type == "aos";
    },
    fetch_gms_notice() {
      this.FETCH_GMS_NOTICE()
        .then((body_data) => {
          console.log(body_data);
          this.notice_gms_list = body_data.notice_gms_list;
          this.notice_gms_lang_list = body_data.notice_gms_lang_list;
          this.notice_gms_arranged_map = {}
          this.notice_gms_lang_list.forEach((item) => {
            let gms_lang_list = this.notice_gms_arranged_map[item.seq_notice];
            if(gms_lang_list) {
              gms_lang_list.push(item);
            }
            else {
              this.notice_gms_arranged_map[item.seq_notice] = []
              this.notice_gms_arranged_map[item.seq_notice].push(item);
            }
          });
        });
    },
    editItem (item) {
      this.is_edit = true;
      this.save_error = "";
      this.dialog = true;
      this.editedIndex = this.notice_gms_list.indexOf(item);
      const seq_notice = item.seq_notice;

      this.editedLangList = this.notice_gms_arranged_map[seq_notice];
      this.editedLangList = this.editedLangList || [];
      this.editedItem = Object.assign({}, item)
    },
    deleteItem (item) {
      if(!confirm('해당 공지를 삭제 하시겠습니까?')) return;

      this.DELETE_GMS_NOTICE(item)
        .then(() => {
          const index = this.notice_gms_list.indexOf(item)
          this.notice_gms_list.splice(index, 1)
        })
      
    },
    close () {
      this.dialog = false
    },
    check_null_value(value, ref, message) {
      if(!value) {
          this.$nextTick(() => ref.focus())
          this.save_error = message;  
          return false;
        }
        return true;
    },
    save () {
      if(!this.check_null_value(this.editedItem.start_date, this.$refs.start_date, "시작 시간을 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.end_date, this.$refs.end_date, "종료 시간을 입력해주세요")) return;

      if(this.is_edit) {
        this.UPDATE_GMS_NOTICE({gms_notice_info: this.editedItem, gms_notice_lang_list: this.editedLangList}).then(() =>{
          this.close();
          this.fetch_gms_notice()
        })
        .catch((err) => {
          this.save_error = err;
        }) 
      }
      else {
        this.INSERT_GMS_NOTICE({gms_notice_info: this.editedItem, gms_notice_lang_list: this.editedLangList}).then(() =>{
          this.close();
          this.fetch_gms_notice()
        })
        .catch((err) => {
          this.save_error = err;
        }) 
      }
    },
    onCreate() {
      this.save_error = "";
      this.dialog = true
      this.is_edit=false;
      this.editedItem = {}
    },
  }
};
</script>
