<template>
  <v-container>
    <v-layout text-center wrap>
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
      <v-container>
        <v-alert v-if="isLive" type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
      </v-container>
      <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
      <v-btn v-if="isLive" color="primary" dark class="mb-2" @click="meta_update_live">메타 파일 서버 적용(Live)</v-btn>
      
    </v-layout>
    <v-data-table
        :headers="headers"
        :items="quration_list"
        sort-by="calories"
        class="elevation-1"
      >
      
    </v-data-table>
    <v-btn @click="export_file">파일 추출</v-btn>
    <v-file-input label="파일 입력" @change="import_file"></v-file-input>
  </v-container>
</template>

<script src="xlsx.full.min.js"></script>
<script src="js/jhxlsx.js"></script>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
import config from '../../src/config/config';

const dateFormat = require('dateformat');
const {exportPurgeTxt} = require("../util/fileutil");

export default {
  name: 'ChapterInfo',
  data() {
    return {
      updating: '',
      quration_list: [],
      headers: [
        { text: '인덱스', value: 'seq_quration' },
        { text: '그룹아이디', value: 'group_id', sortable: false },
        { text: '카테고리', value: 'category', sortable: false },
        { text: '질문대사', value: 'question', sortable: false },
        { text: '프로그래스', value: 'progress', sortable: false },
        { text: '질문', value: 'question_order', sortable: false },
        { text: '대답', value: 'answer', sortable: false },
        { text: '링크 타입', value: 'link_type', sortable: false },
        { text: '링크 아이디', value: 'link_id', sortable: false },
        { text: 'funnel_id', value: 'funnel_id', sortable: false },
      ],
    }
  },
  created() {
    this.fetch_quration_list();
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_QURATION_LIST',
      'UPDATE_QURATION_LIST',
      'META_QURATION_UPDATE',
      'META_QURATION_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_QURATION_UPDATE();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      const result = await this.META_QURATION_UPDATE_LIVE();
      exportPurgeTxt(result.purge_list, `QurationMeta_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = ""
    },
    fetch_quration_list() {
      this.chapter_root_list = [];

      this.$nextTick(() => {
        this.FETCH_QURATION_LIST()
        .then((body_data) => {
          this.quration_list = body_data.quration_list;
          
        })
      })
    },
    close () {
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          console.log(workbook.Sheets[sheetName]);
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_QURATION_LIST({quration_list: json_object})
            .then(() => {
              this.fetch_quration_list();
            });
        })
      };

      reader.onerror = function(ex) {
          console.log(ex);
      };

      reader.readAsBinaryString(file);
    },
    export_file() {
        
        let headers = ['seq_quration','group_id','category','question','progress','question_order','link_type','link_id','answer']
        if(this.quration_list.length == 0 ) {
          this.quration_list.push({
            'seq_quration': '번호',
            'group_id': '그룹아이디',
            'category': '카테고리',
            'question': '질문',
            'progress': '프로그래스',
            'question_order': '질문 오더',
            'link_type': '링크 타입',
            'link_id': '링크 아이디',
            'answer': '답변'
          })
        }
        var ws = XLSX.utils.json_to_sheet(this.quration_list, {headers});
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"quration");
        XLSX.writeFile(wb, `Quration.xlsx`);
    },
    onCreate() {

    }
  }
};
</script>
