<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="costume_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>선택지</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.seq_chapter" label="챕터"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.base_choice" label="base_choice"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.target_choice" label="target_choice"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.factor" label="팩터"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.condition" label="조건"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      costume_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: '캐릭터 일련번호', value: 'seq_character' },
        { text: '의상 코드', value: 'costume_code' },
        { text: '의상 값', value: 'costume_value' },
        { text: '의상 타입', value: 'costume_type' },
        { text: '기본 의상', value: 'is_base' },
        { text: '의상 이름', value: 'costume_name' },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    console.log(this.seq_title);
    this.FETCH_COSTUME_LIST(this.seq_title)
      .then((body_data) => {this.costume_list = body_data.costume_list});
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_COSTUME_LIST'
    ]),
    editItem (item) {
      this.editedIndex = this.costume_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.costume_list.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.costume_list.splice(index, 1)
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.costume_list[this.editedIndex], this.editedItem)
      } else {
        this.costume_list.push(this.editedItem)
      }
      this.close()
    },
  }
};
</script>
