<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="ending_list"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>엔딩</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <!-- <v-btn color="primary" dark class="mb-2" @click="onCreateItem">New Item</v-btn> -->
          <v-dialog v-model="dialog" max-width="500px">
            
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.id" disabled label="엔딩 id"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.name" label="이름" ref="name"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.summary" label="요약" ref="summary"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.condition_error" label="조건 에러" ref="condition_error"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      ending_list: [],
      seq_title: 0,
      dialog: false,
      is_edit: false,
      save_error: '',
      headers: [
        { text: '엔딩아이디', value: 'id' },
        { text: '제목', value: 'name' },
        { text: '요약', value: 'summary' },
        { text: '조건 에러', value: 'condition_error' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    console.log(this.seq_title);
    this.FETCH_ENDING_LIST(this.seq_title)
      .then((body_data) => {this.ending_list = body_data.ending_list});
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_ENDING_LIST',
      'UPDATE_ENDING',
    ]),
    onCreateItem() {
      this.is_edit = false;
      this.dialog = true
      this.editedItem = {}
    },
    editItem (item) {
      this.editedIndex = this.ending_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.ending_list.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.ending_list.splice(index, 1)
    },
    close () {
      this.dialog = false
    },
    check_null_value(value, ref, message) {
      if(!value) {
          this.$nextTick(() => ref.focus())
          this.save_error = message;  
          return false;
        }
        return true;
    },
    save () {
      if(!this.check_null_value(this.editedItem.name, this.$refs.name, "엔딩 제목을 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.summary, this.$refs.summary, "엔딩 요약을 입력해주세요")) return;

      this.editedItem.seq_title = this.seq_title;
      this.UPDATE_ENDING(this.editedItem)
        .then(() => {
          this.FETCH_ENDING_LIST(this.seq_title)
            .then((body_data) => {this.ending_list = body_data.ending_list});

          this.close()
        })
    },
  }
};
</script>
