<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    
      <v-data-table
        dense
        :headers="headers"
        :items="product_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>상품 리스트(성인)</v-toolbar-title>
            
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-btn color="primary" dark class="mb-2" @click="onCreate">New Item</v-btn>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="4">
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field disabled v-model="editedItem.seq_product" label="일련번호"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.product_name" label="상품명"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.active_yn"
                            :items="active_yn_list"
                            item-text="text"
                            item-value="value"
                            label="활성화 여부"
                            persistent-hint
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.tab_id"
                            :items="tab_list"
                            item-text="text"
                            item-value="value"
                            label="탭 종류"
                            persistent-hint
                            return-object
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.show_type"
                            :items="show_type_list"
                            item-text="text"
                            item-value="value"
                            label="표현 방식"
                            persistent-hint
                            return-object
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.limit_type"
                            :items="limit_type_list"
                            item-text="text"
                            item-value="value"
                            label="제한 방식"
                            persistent-hint
                            return-object
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.limit_value" label="제한 값"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.pay_type"
                            :items="pay_type_list"
                            item-text="text"
                            item-value="value"
                            label="지불 방법"
                            persistent-hint
                            return-object
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.cost" label="가격"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.aos_key" label="aos_key"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.ios_key" label="ios_key"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.display_yn"
                            :items="active_yn_list"
                            item-text="text"
                            item-value="value"
                            label="활성화 여부"
                            persistent-hint
                            
                          ></v-select>
                        </v-col>
                        <v-col cols="12">
                          <img v-if="image_url" :src="image_url"/>
                          <v-file-input accept="image/*" label="File input" @change="onImageChange"></v-file-input>
                        </v-col>

                      </v-row>
                    </v-col>  
                    <v-col cols="12" sm="8">
                      <v-container>
                        <v-row>
                          
                          <v-col cols="12" sm="4">
                            <v-select
                                v-model="add_item.reward_type"
                                :items="reward_type_list"
                                item-text="text"
                                item-value="value"
                                label="보상 타입"
                                persistent-hint
                                
                            ></v-select>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-select
                                v-model="add_item.item_category"
                                :items="item_category_list"
                                item-text="text"
                                item-value="value"
                                label="아이템 카테고리"
                                persistent-hint
                                
                            ></v-select>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-select
                                v-model="add_item.item_id"
                                :items="getItemId()"
                                item-text="text"
                                item-value="value"
                                label="아이템 아이디"
                                persistent-hint
                            ></v-select>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-text-field v-model="add_item.item_qny" label="아이템 개수"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-select
                                v-model="add_item.seq_title"
                                :items="title_list"
                                item-text="name"
                                item-value="seq_title"
                                label="타이틀 리스트"
                                persistent-hint
                            ></v-select>
                          </v-col>
                          <v-col cols="12" sm="4">
                            <v-btn @click="addItem">추가</v-btn>
                          </v-col>
                          </v-row>
                        <v-divider></v-divider>
                        <div v-for="(item, index) in itemEditList" :key="index">
                          {{item.item_category}}
                          {{item.item_id}}
                          X {{item.item_qny}}
                          {{get_title_name(item.seq_title)}}
                          <v-btn @click="deleteProductItem(item)">삭제</v-btn>
                        </div>
                      </v-container>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.product_item="{ item }">
          <div v-for="(product_item, index) in product_item_arranged_map[item.seq_product]" :key="index">
            {{product_item.item_category}}
            {{product_item.item_id}}
            X {{product_item.item_qny}}
            {{get_title_name(product_item.seq_title)}}
          </div>
        </template>
        <template v-slot:item.product_key="{ item }">
          <div v-if="item.aos_key">
            {{item.aos_key}}
          </div>
          <div v-if="item.ios_key">
            {{item.ios_key}}
          </div>
        </template>
        <template v-slot:item.banner_img="{ item }">
          <img v-if="item.banner_img" :src="get_item_img(item)" width="120"/>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
      
      <v-row>
        <v-col>
          <v-btn @click="export_file">상품 리스트</v-btn>
          <v-file-input label="상품 리스트" @change="import_file"></v-file-input>  
        </v-col>
        <v-col>
          <v-btn @click="export_item_file">상품 아이템 리스트</v-btn>
          <v-file-input label="상품 아이템 리스트" @change="import_item_file"></v-file-input>  
          <v-alert v-if="error_file_item" type="error">{{error_file_item}}</v-alert>
          
        </v-col>
      </v-row>
    
    </v-layout>
  </v-container>
</template>
<script src="xlsx.full.min.js"></script>
<script src="js/jhxlsx.js"></script>

<script>
import XLSX from 'xlsx';
import { mapActions, mapState } from 'vuex';
import _ from 'lodash'

const no_image = require(`../assets/no_image.jpg`);

var crc = require('crc');
const {s3_upload} = require("../util/fileutil");

const product_file_name = `PRODUCT_ADULT.xlsx`;
const product_item_file_name = `PRODUCT_ADULT_ITEM.xlsx`;

const product_type = "adult"
export default {
  name: 'ProductInfo',
  data() {
    return {
      add_item: {},
      itemList: [],
      itemEditList: [],
      title_list: [],
      title_arranged_map: null,
      image_url: "",
      image_file: {},
      patch_info: null,
      error_file_item: '',
      tab_list: [
        {text: '젬', value: 'GEM'},
        {text: '에너지', value: 'ENERGY'},
        {text: '패키지', value: 'PACKAGE'},
        {text: '미표시', value: 'NONE'},
      ],
      reward_type_list: [
        {text: '일반', value: 'NORMAL'},
        {text: '보너스', value: 'BONUS'},
        {text: '매일 지급', value: 'MONTH_SUB'},
        {text: '능력치', value: 'STAT_UP'},
      ],
      show_type_list: [
        {text: '싱글', value: 'SINGLE'},
        {text: '배너', value: 'BANNER'},
      ],
      limit_type_list: [
        {text: '제한 없음', value: 'NO_LIMIT'},
        {text: '월 구독', value: 'MONTH_SUB'},
        {text: '일일', value: 'DAILY'},
        {text: '계정', value: 'ACCOUNT'},
      ],
      pay_type_list: [
        {text: '현금', value: 'CASH'},
        {text: '젬', value: 'GEM'},
      ],
      active_yn_list: [
        {text: "활성화", value: "Y"},
        {text: "비활성화", value: "N"},
      ],
      item_category_list: [
        {text: "재화", value: "GOOD"},
        {text: "티켓", value: "TICKET"},
        {text: "스탯 제한치", value: "STAT_LIMIT"},
      ],
      good_item_list: [
        {text: "젬", value: "GEM"},
        {text: "무료젬", value: "FREE_GEM"},
        {text: "에너지", value: "ENERGY"},
      ],
      ticket_item_list: [
        {text: "소장권", value: "POSSESSION"},
        {text: "대여권", value: "RENTAL"},
      ],
      stat_limit_list: [
        {text: "에너지 제한", value: "ENERGY_LIMIT"},
      ],
      insert: false,
      product_list: [],
      dialog: false,
      headers: [
        { text: '일련번호', value: 'seq_product' },
        { text: '상품명', value: 'product_name' },
        { text: '활성화여부', value: 'active_yn' },
        { text: '탭', value: 'tab_id' },
        { text: '표현 타입', value: 'show_type' },
        { text: '제한 타입', value: 'limit_type' },
        { text: '제한 값', value: 'limit_value' },
        { text: '지불 타입', value: 'pay_type' },
        { text: '비용', value: 'cost' },
        { text: '보상 아이템', value: 'product_item' },
        { text: '상품 키', value: 'product_key' },
        { text: 'banner_img', value: 'banner_img' },
        { text: '활성화여부', value: 'display_yn' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.fetch_product_list();
  },
  computed: {
    ...mapState({
        CDN_URL: 'CDN_URL'
    }),
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'FETCH_PRODUCT_LIST',
      'UPDATE_PRODUCT',
      'UPDATE_PRODUCT_LIST',
      'UPDATE_PRODUCT_ITEM_LIST',
      'INSERT_PRODUCT',
      'DELETE_PRODUCT',
      'META_PRODUCT_UPDATE',
      'OPERATE_SERVER'
    ]),
    get_title_name(seq_title) {
      if(seq_title) {
        return this.title_arranged_map[seq_title].name;
      }
    },
    addItem() {
      this.itemEditList.push(Object.assign({}, this.add_item));
      this.add_item = {}
    },
    getItemId() {
      if(this.add_item.item_category == "TICKET") {
        return this.ticket_item_list;
      }
      else if(this.add_item.item_category == "GOOD") {
        return this.good_item_list;
      }
      else if(this.add_item.item_category == "STAT_LIMIT") {
        return this.stat_limit_list;
      }
    },
    createProduct() {
      
    },
    get_item_img(item) {
      return `${this.CDN_URL}${item.banner_img}`;
    },
    get_title_img() {
      if (this.image_file) {
        return this.image_file.thumb;
      }

      return `${this.CDN_URL}${this.editedItem.banner_url}`;
    },
    fetch_product_list() {
      this.product_list = [];

      this.$nextTick(() => {
        this.FETCH_PRODUCT_LIST({product_type})
        .then((body_data) => {
          this.product_list = body_data.product_list
          this.itemList = body_data.product_item_list;
          this.product_item_arranged_map = {};

          this.itemList.forEach((item) => {
            let seq_item_list = this.product_item_arranged_map[item.seq_product];
            if(seq_item_list) {
              seq_item_list.push(item);
            }
            else {
              this.product_item_arranged_map[item.seq_product] = []
              this.product_item_arranged_map[item.seq_product].push(item);
            }
          });

          this.patch_info = body_data.patch_info;
          this.title_list = body_data.title_list;
          this.title_arranged_map = _.keyBy(this.title_list, "seq_title");
        });
      })
    },
    onImageChange(upload_file) {
      if(!upload_file) {
        this.image_file = {};
        return;
      }
      
      upload_file.arrayBuffer().then((data) => {
        this.image_file.file = upload_file;
        this.image_file.crc = crc.crc32(data).toString(16);
        this.image_file.thumb = URL.createObjectURL(upload_file);
        this.image_url = URL.createObjectURL(upload_file);
      }) 
    },
    editItem (item) {
      this.insert = false;
      this.editedIndex = this.product_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.image_url = this.editedItem.banner_img;

      this.itemEditList = this.product_item_arranged_map[item.seq_product];
      if(!this.itemEditList) this.itemEditList = [];
      
      this.dialog = true
    },
    deleteItem(item) {
      if(!confirm('해당 상품을 삭제 하시겠습니까?')) return;
      this.DELETE_PRODUCT({product_info: item, product_type})
        .then(() => {
          this.fetch_product_list();
        })
    },
    deleteProductItem(item) {
      const index = this.itemEditList.indexOf(item)
      this.itemEditList.splice(index, 1)
    },
    onCreate(item) {
      this.insert = true;
      this.dialog = true;
      this.editedItem = {}
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      
      if(this.editedItem.tab_id instanceof Object) this.editedItem.tab_id = this.editedItem.tab_id.value;
      if(this.editedItem.show_type instanceof Object) this.editedItem.show_type = this.editedItem.show_type.value;
      if(this.editedItem.limit_type instanceof Object) this.editedItem.limit_type = this.editedItem.limit_type.value;
      if(this.editedItem.pay_type instanceof Object) this.editedItem.pay_type = this.editedItem.pay_type.value;
      if(this.image_file.file) this.editedItem.banner_img = `images/${this.patch_info}/${this.image_file.file.name}`

      if(this.insert) {
        this.INSERT_PRODUCT({product_info: this.editedItem, product_type, product_item_list: this.itemEditList}).then(async() => {
          if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
          this.image_file = {};
          this.close();
          this.fetch_product_list();
        })
      }
      else {
        this.UPDATE_PRODUCT({product_info: this.editedItem, product_type, product_item_list: this.itemEditList}).then(async() => {
          if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
          this.image_file = {};
          this.close();
          this.fetch_product_list();
        })
      }
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_PRODUCT_LIST({product_list: json_object, product_type})
            .then(() => {
              this.fetch_product_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_file() {
        var ws = XLSX.utils.json_to_sheet(this.product_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"product");
        XLSX.writeFile(wb, product_file_name);
    },
    import_item_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_PRODUCT_ITEM_LIST({product_item_list: json_object, product_type})
            .then((response_body) => {
              let wrong_product = response_body.wrong_product;

              if(wrong_product && wrong_product.length) {
                this.error_file_item = `${wrong_product.join(',')} - 잘못된 seq_product `
              }

              this.fetch_product_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_item_file() {
        var ws = XLSX.utils.json_to_sheet(this.itemList);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"product_item");
        XLSX.writeFile(wb, product_item_file_name);
    },

  }
};
</script>
