import { setAuthInHeader } from '../api'

const mutations = {
    SET_IS_ADD_BOARD(state, toggle) {
        state.isAddBoard = toggle
    },
    SET_BOARDS(state, boards) {
        console.log('SET_BOARDS');
        state.boards = boards;
    },
    LOGIN(state, { session_id, user_id }) {
        if (!session_id) return;

        state.session_id = session_id;
        state.user_id = user_id;

        localStorage.setItem('session_id', session_id);
        localStorage.setItem('user_id', user_id);
        setAuthInHeader(session_id);
    },
    LOGOUT(state) {
        state.session_id = null;
        state.user_id = null;
        delete localStorage.session_id;
        delete localStorage.user_id;
        setAuthInHeader(null);
    },
    SET_BOARD(state, board) {
        state.board = board;
    },
    SET_CARD(state, card) {
        state.card = card;
    },
    SET_THEME(state, color) {
        console.log('SET_THEME', color);
        state.bodyColor = color || '#ffffff';
        state.navbarColor = color ? 'rgba(0,0,0,.15)' : '#026aa7';
    },
    SET_IS_SHOW_BOARD_SETTINGS(state, toggle) {
        state.isShowBoardSettings = toggle;
    }
}

export default mutations;