<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      
      <v-container>
        <v-btn color="primary" v-if="!is_editable" dark class="mb-2" @click="onEdit">수정</v-btn>
        <v-btn color="primary" v-else dark class="mb-2" @click="onSave">저장</v-btn>
        
      </v-container>
      <v-container outline class="grey lighten-5">
        <v-row>
          <v-col  cols=3>
            <v-card>
              <v-row>
                <v-col >
                  활성화
                </v-col>
                <v-col>
                  <v-switch :disabled="!is_editable" class="flag" v-model="title_info.active_flag" :label="get_switch_flag()"></v-switch>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  제목
                </v-col>
                <v-col>
                  <v-text-field
                    :disabled="!is_editable"
                    v-model="title_info.name"
                    placeholder="타이틀 제목"
                  ></v-text-field>
                  
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  이미지
                </v-col>
                <v-col>
                  <img :src="get_title_img()" width="212px"/>
                  <v-file-input v-if="is_editable" accept="image/*" label="File input" @change="onThumbChange"></v-file-input>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            
            <v-card>
              <v-row>
                <v-col>
                  new 이미지
                </v-col>
                <v-col>
                  <img :src="get_title_new_img()" width="212px"/>
                  <v-file-input v-if="is_editable" accept="image/*" label="File input" @change="onNewThumbChange"></v-file-input>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  타이틀 무료 대기시간
                </v-col>
                <v-col>
                  <v-text-field
                    :disabled="!is_editable"
                    v-model="title_info.wait_free_time"
                    placeholder="타이틀 무료 대기시간"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            
          </v-col>
          <v-col>
            <v-card>
              <v-row>
                <v-col>
                  타이틀 타입          
                </v-col>
                <v-col>
                  <v-radio-group :disabled="!is_editable" v-model="title_info.label_type" :mandatory="false" row>
                    <v-radio label="일반" value="NONE"></v-radio>
                    <v-radio label="사전예약" value="RESERVATION"></v-radio>
                    <v-radio label="최근출시" value="RECENTLY"></v-radio>
                    <v-radio label="핫!" value="HOT"></v-radio>
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  타이틀 등급          
                </v-col>
                <v-col>
                  <v-radio-group :disabled="!is_editable" v-model="title_info.view_rating" :mandatory="false" row>
                    <v-radio label="전체" value="ALL"></v-radio>
                    <v-radio label="성인" value="ADULT"></v-radio>
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  옷장 사용여부
                </v-col>
                <v-col>
                  <v-radio-group :disabled="!is_editable" v-model="title_info.closet_active_yn" :mandatory="false" row>
                    <v-radio label="사용" value="Y"></v-radio>
                    <v-radio label="미사용" value="N"></v-radio>
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  스핀오프 사용 여부
                </v-col>
                <v-col>
                  <v-radio-group :disabled="!is_editable" v-model="title_info.spinoff_active_yn" :mandatory="false" row>
                    <v-radio label="사용" value="Y"></v-radio>
                    <v-radio label="미사용" value="N"></v-radio>
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  연재요일
                </v-col>
                <v-col>
                  <v-select
                    :disabled="!is_editable"
                    v-model="title_info.publish_day"
                    :items="day_items"
                    item-text="text"
                    item-value="value"
                    label="Select"
                    persistent-hint
                    return-object
                    
                  ></v-select>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  글
                </v-col>
                <v-col>
                  <v-text-field
                    :disabled="!is_editable"
                    v-model="title_info.author"
                    placeholder="작가"
                  ></v-text-field>
                </v-col>
              </v-row>

            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  그림
                </v-col>
                <v-col>
                  <v-text-field
                    :disabled="!is_editable"
                    v-model="title_info.drawer"
                    placeholder="그림"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  원작
                </v-col>
                <v-col>
                  <v-text-field
                    :disabled="!is_editable"
                    v-model="title_info.original"
                    placeholder="원작"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  출시일자
                </v-col>
                <v-col>
                  <v-datetime-picker :disabled="!is_editable" v-model="title_info.release_date"> </v-datetime-picker>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col cols=12 sm=2>
                  티켓 가격
                </v-col>
                <v-col cols=12 sm=10>
                  <div v-if="ticket_purchase_list.length">
                    
                    <div v-for="(ticket_purchase, index) in ticket_purchase_list" :key="index">
                      <v-row >
                        <v-col cols=12 sm=3>{{ticket_purchase.ticket_type}}</v-col>
                        <v-col cols=12 sm=2>{{ticket_purchase.pay_key}}</v-col>
                        <v-col>
                          <v-text-field
                            :disabled="!is_editable"
                            v-model="ticket_purchase.ticket_cost"
                            placeholder="비용"
                          ></v-text-field>
                        </v-col>
                      
                        <v-col>
                          <v-text-field
                            :disabled="!is_editable"
                            v-model="ticket_purchase.ticket_qny"
                            placeholder="티켓"
                          ></v-text-field>
                        </v-col>
                        <v-col>
                          <v-text-field
                            :disabled="!is_editable"
                            v-model="ticket_purchase.ticket_bonus_qny"
                            placeholder="보너스 티켓"
                          ></v-text-field>
                        </v-col>
                        </v-row>
                    </div>
                  </div>
                  <div v-else>
                    <v-btn @click="createTicketData">타이틀 티켓 기본 데이터 생성</v-btn>
                  </div>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
          </v-col>
          <v-col>
            <v-card>
              <v-row>
                <v-col>
                  타이틀 컬러
                </v-col>
                <v-col class="shrink" style="min-width: 220px;">
                  <v-text-field v-model="title_info.title_color" hide-details class="ma-0 pa-0" solo>
                    <template v-slot:append>
                      <v-menu v-model="title_color_menu" top nudge-bottom="105" nudge-left="16" :close-on-content-click="false">
                        <template v-slot:activator="{ on }">
                          <div :style="swatchStyle" v-on="on" />
                        </template>
                        <v-card>
                          <v-card-text class="pa-0">
                            <v-color-picker v-model="title_info.title_color" flat />
                          </v-card-text>
                        </v-card>
                      </v-menu>
                    </template>
                  </v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  메인 태그
                </v-col>
                <v-col>
                  <v-select
                    :items="title_category_info.title_main_category_list"
                    item-text="name"
                    item-value="seq_category"
                    label="Select"
                    persistent-hint
                    return-object
                    
                    @change="onMaintagChange"
                  ></v-select>
                </v-col>
                <v-col>
                  <v-chip v-if="selected_main_tag" :color="selected_main_tag.background_color">{{selected_main_tag.name}}</v-chip>
                </v-col>
                <v-col>
                  <v-btn :disabled="!selected_main_tag" @click="onLinkMainTag">등록</v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <div class="selected_list">
                    <v-chip 
                      v-for="category in title_category_info.selected_main_category" 
                      :color="category.background_color"
                      :key="category.seq_category"
                      close
                      @click:close="unlinkCategory(category, 'main')">{{category.name}}</v-chip>
                  </div>                  
                </v-col>
              </v-row>

            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  서브 태그
                </v-col>
                <v-col>
                  <v-select
                    :items="title_category_info.title_sub_category_list"
                    item-text="name"
                    item-value="seq_category"
                    label="Select"
                    persistent-hint
                    return-object
                    
                    @change="onSubtagChange"
                  ></v-select>
                </v-col>
                <v-col>
                  <v-chip v-if="selected_sub_tag" :color="selected_sub_tag.background_color">{{selected_sub_tag.name}}</v-chip>
                </v-col>
                <v-col>
                  <v-btn :disabled="!selected_sub_tag" @click="onLinkSubTag">등록</v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <div class="selected_list">
                    <v-chip 
                      v-for="category in title_category_info.selected_sub_category" 
                      :color="category.background_color"
                      :key="category.seq_category"
                      close
                      @click:close="unlinkCategory(category, 'sub')">{{category.name}}</v-chip>
                  </div>                  
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col>
                  <v-textarea
                    :disabled="!is_editable"
                    label="짧은 줄거리"
                    v-model="title_info.short_summary"
                    hint="Hint text"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col cols=12>
                  <v-textarea
                    :disabled="!is_editable"
                    label="긴 줄거리"
                    v-model="title_info.summary"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-card>
            <v-divider/>
            <v-card>
              <v-row>
                <v-col cols=12>
                  <v-textarea
                    :disabled="!is_editable"
                    label="플레이 방법"
                    v-model="title_info.how_to_play"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-card>
          </v-col>
          
        </v-row>
        
      </v-container>
    </v-layout>
  </v-container>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import _ from 'lodash'

var crc = require('crc');
const {s3_upload} = require("../util/fileutil");

var dateFormat = require('dateformat')
const no_image = require(`../assets/no_image.jpg`);
export default {
  name: 'TitleInfo',
  data() {
    return {
      ticket_purchase_list: [],
      updating: "",
      selected_main_tag: null,
      selected_sub_tag: null,
      thumb_file:null,
      new_thumb_file:null,
      title_info: {},
      chapter_count_info: {},
      title_category_info: {},
      seq_title: 0,
      is_editable: false,
      patch_info: null,
      day_items: [
        {text: "없음", value: "none"},
        {text: "월", value: "mon"},
        {text: "화", value: "tue"},
        {text: "수", value: "wed"},
        {text: "목", value: "thr"},
        {text: "금", value: "fri"},
        {text: "토", value: "sat"},
        {text: "일", value: "sun"},
        {text: "완결", value: "comp"},
      ],
      title_color_menu: false,
      mask: '!#XXXXXX',
      test_color: "#121212"
    }
  },
  computed: {
    ...mapState({
      CDN_URL: 'CDN_URL'
    }),
    swatchStyle() {
      const { title_color, title_color_menu } = this.title_info
      return {
        backgroundColor: title_color,
        cursor: 'pointer',
        height: '30px',
        width: '30px',
        borderRadius: title_color_menu ? '50%' : '4px',
        transition: 'border-radius 200ms ease-in-out'
      }
    },
    is_inlist(category, type) {
      console.log("is_inlist");
      if( type == "main") {
        return this.title_category_info.selected_main_category.indexOf(category) >= 0
      }
      else {
        return this.title_category_info.selected_sub_category.indexOf(category) >= 0
      }
    }, 
  },
  watch: {

  },
  methods: {
    ...mapActions([
      'FETCH_TITLE_INFO',
      'UPDATE_TITLE',
      'LINK_TITLE_CATEGORY',
      'UNLINK_TITLE_CATEGORY',
      'META_TITLE_UPDATE_SEQ_TITLE',
      'OPERATE_SERVER',
      'MAKE_BASE_PURCHASE_TICKET_DATA'
    ]),
    createTicketData() {
      this.MAKE_BASE_PURCHASE_TICKET_DATA(this.seq_title);
    },
    get_title_img() {
      if (this.thumb_file) {
        return this.thumb_file.thumb;
      }
      return this.title_info.thumb ? `${this.CDN_URL}${this.title_info.thumb}` : no_image;
    },
    get_title_new_img() {
      if (this.new_thumb_file) {
        return this.new_thumb_file.new_image_url;
      }

      return this.title_info.new_image_url ? `${this.CDN_URL}${this.title_info.new_image_url}` : no_image;
    },
    invertHex(hex) {
      hex = hex.replace("#","")
      return "#" + (Number(`0x1${hex}`) ^ 0xFFFFFF).toString(16).substr(1).toUpperCase()
    },
    get_switch_flag() {
      return this.title_info.active_flag ? "활성" : "비활성"
    },
    onLinkMainTag() {
      this.LINK_TITLE_CATEGORY({seq_title: this.seq_title, title_category_info: this.selected_main_tag})
        .then(() => {
          this.selected_main_tag.is_show = true;
          this.title_category_info.selected_main_category.push(this.selected_main_tag)
          this.selected_main_tag = null;
        })
    },
    onLinkSubTag() {
      this.LINK_TITLE_CATEGORY({seq_title: this.seq_title, title_category_info: this.selected_sub_tag})
        .then(() => {
          this.selected_sub_tag.is_show = true;
          this.title_category_info.selected_sub_category.push(this.selected_sub_tag);
          this.selected_sub_tag = null;
        })
    },
    unlinkCategory(category, type) {
      this.UNLINK_TITLE_CATEGORY({seq_title: this.seq_title, title_category_info: category})
        .then(() => {
          if(type == "main") {
            const index = this.title_category_info.selected_main_category.indexOf(category)
            this.title_category_info.selected_main_category[index].seq_category = 1;
            this.title_category_info.selected_main_category.splice(index, 1);
            
            // this.title_category_info.selected_main_category.splice(index, 1);
          }
          else {
            const index = this.title_category_info.selected_sub_category.indexOf(category)
            this.title_category_info.selected_sub_category[index].seq_category = 1;
            this.title_category_info.selected_sub_category.splice(index, 1);
          }
        })
    },
    async onSave() {
      if( !this.title_info.name ) {
        this.$nextTick(() => this.$refs.inputTitle.focus())
        this.save_error = '타이틀을 입력해주세요';
        return;
      }

      // 기타 데이터 세팅
      if(this.thumb_file) {
        this.title_info.thumb = `images/${this.patch_info}/${this.thumb_file.name}`;
        this.title_info.crc_thumb = this.thumb_file.crc;
      }
      
      if (this.new_thumb_file) {
        this.title_info.new_image_url = `images/${this.patch_info}/${this.new_thumb_file.name}`;
        this.title_info.new_image_crc = this.new_thumb_file.crc;
      }
      
      this.title_info.publish_day = this.title_info.publish_day.value;
      //this.title_info.release_date = this.title_info.release_date.getTime();

      // 데이터 입력
      this.title_info.active_flag = this.title_info.active_flag ? "Y" : "N";
      this.title_info.patch_version = this.patch_info;

      this.UPDATE_TITLE({title_info: this.title_info, ticket_purchase_list: this.ticket_purchase_list}).then( async () => {
        console.log("update_success")
        console.log(this.thumb_file);
        console.log(this.new_thumb_file);
        this.dialog = false
        this.is_editable = false;
        // 성공하면 파일 업로드
        if( this.thumb_file ) await s3_upload(this.thumb_file, `images/${this.patch_info}/${this.thumb_file.name}`);
        if( this.new_thumb_file ) await s3_upload(this.new_thumb_file, `images/${this.patch_info}/${this.new_thumb_file.name}`);
        this.fetch_title_info();
      })
    },
    onMaintagChange(arg) {
      this.selected_main_tag = arg
    },
    onSubtagChange(arg) {
      this.selected_sub_tag = arg
    },
    onEdit() {
      this.is_editable = true;
      
    },
    onThumbChange(upload_file) {
      if(!upload_file) {
        this.thumb_file = null;
        return;
      }
      
      upload_file.arrayBuffer().then((data) => {
        this.thumb_file = upload_file;
        this.thumb_file.crc = crc.crc32(data).toString(16);
        this.thumb_file.thumb = URL.createObjectURL(upload_file);
      }) 
    },
    onNewThumbChange(upload_file) {
      if(!upload_file) {
        this.new_thumb_file = null;
        return;
      }

      upload_file.arrayBuffer().then((data) => {
        this.new_thumb_file = upload_file;
        this.new_thumb_file.crc = crc.crc32(data).toString(16);
        this.new_thumb_file.new_image_url = URL.createObjectURL(upload_file);
      }) 
    },
    fetch_title_info() {
      this.FETCH_TITLE_INFO(this.seq_title).then((body_data) => {
        this.patch_info = body_data.patch_info;
        this.title_info = body_data.title_info;
        this.ticket_purchase_list = body_data.ticket_purchase_list;
        this.title_info.active_flag = this.title_info.active_flag == "Y" ? true : false;

        
        this.chapter_count_info = body_data.chapter_count_info;
        this.title_category_info = {
          title_category_list: body_data.title_category_list,
          title_category_link_list: body_data.title_category_link_list,
        }

        this.title_category_info.title_main_category_list = this.title_category_info.title_category_list.filter((item) => item.main_yn == "Y")
        this.title_category_info.title_sub_category_list = this.title_category_info.title_category_list.filter((item) => item.main_yn == "N")

        let category_map = _.keyBy(this.title_category_info.title_category_list, 'seq_category');

        this.title_category_info.selected_main_category = []
        this.title_category_info.selected_sub_category = []
        this.title_category_info.title_category_link_list.forEach((item) => {
          let category_info = category_map[item.seq_category];
          if(!category_info) return;

          category_info.is_show = true;
          category_info.main_yn == "Y" ? 
            this.title_category_info.selected_main_category.push(category_info) :
            this.title_category_info.selected_sub_category.push(category_info);
        })
      });
    },
  },
  
  created() {
    console.log(dateFormat(new Date(), "yyyy-mm-dd"));
    this.seq_title = this.$route.params.seq_title;
    this.fetch_title_info();
    
  }
};
</script>

<style>
.selected_list {
  background-color: lightblue;
  padding: 1%
}
.v-card > .row > .col{
  align-self: center;
}
.v-text-field textarea {
    font-size: 0.8em;
  }
</style>