<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-dialog v-model="resource_dialog">
        <v-container>
        <v-card>
        <v-btn @click="change_resource_type('aos')">AOS</v-btn>
        <v-btn @click="change_resource_type('ios')">IOS</v-btn>
        <div v-if="aos_resource">
          <table>
            <tr v-for="item in aos_resource_list" :key="item.id">
              <td><a :href="`${s3_url}android/${item.url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        <div v-if="!aos_resource">
          <table>
            <tr v-for="item in ios_resource_list" :key="item.id">
              <td><a :href="`${s3_url}ios/${item.url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        </v-card>
        </v-container>
      </v-dialog>
      <v-dialog v-model="illust_dialog" max-width="500px">
        <v-container>
        <v-card>
        <v-btn @click="change_resource_type('aos')">AOS</v-btn>
        <v-btn @click="change_resource_type('ios')">IOS</v-btn>
        <div v-if="aos_resource">
          <table>
            <tr v-for="item in aos_illust_list" :key="item.id">
              <td><a :href="`${s3_url}android/${url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        <div v-if="!aos_resource">
          <table>
            <tr v-for="item in ios_illust_list" :key="item.id">
              <td><a :href="`${s3_url}ios/${url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        
        </v-card>
        </v-container>
      </v-dialog>
    <v-dialog v-model="editConditionDialog" >
      <v-container>
      <v-card>
        <v-row>
          <v-col>일련번호</v-col>
          <v-col>조건타입</v-col>
          <v-col>조건1</v-col>
          <v-col>조건2</v-col>
          <v-col>편집</v-col>
        </v-row>
        <v-divider></v-divider>
        <div v-for="condition in condition_list" :key="condition.seq_condition">
          <v-row>
          <v-col><v-text-field disabled v-model="condition.seq_condition" label="일련번호"></v-text-field></v-col>
          <v-col>
          <v-select
            v-model="condition.con_type"
            :disabled="!condition.editing"
            :items="condition_type"
            item-text="text"
            item-value="value"
            label="조건 타입"
            persistent-hint
            return-object
            
          ></v-select>
          </v-col>
          <v-col>
            <v-select
              v-if="condition.con_type == 'FACTOR' || condition.con_type.value == 'FACTOR'"
              v-model="condition.con_value1"
              :disabled="!condition.editing"
              :items="factor_list"
              item-text="name"
              item-value="seq_factor"
              label="팩터 타입"
              persistent-hint
              return-object
              
            ></v-select>
            <v-select
              v-if="condition.con_type == 'CHAPTER_CLEAR' || condition.con_type.value == 'CHAPTER_CLEAR'"
              v-model="condition.con_value1"
              :disabled="!condition.editing"
              :items="chapter_list"
              item-text="seq_chapter"
              item-value="seq_chapter"
              label="챕터 타입"
              persistent-hint
              return-object
              
            ></v-select>
            <v-select
              v-if="condition.con_type.value == 'ENDING' || condition.con_type == 'ENDING'"
              v-model="condition.con_value1"
              :disabled="!condition.editing"
              :items="ending_list"
              item-text="name"
              item-value="id"
              label="엔딩 타입"
              persistent-hint
              return-object
              
            ></v-select>
          </v-col>
          <v-col>
            <v-text-field 
              v-if="condition.con_type.value == 'FACTOR' || condition.con_type == 'FACTOR'"
              :disabled="!condition.editing" 
              v-model="condition.con_value2" 
              label="조건2"></v-text-field>
          </v-col>
          <v-col>
          <v-btn v-if="!condition.editing" @click="editCondition(condition)">편집</v-btn>
          <v-btn v-if="!condition.editing" @click="deleteCondition(condition)">삭제</v-btn>
          <v-btn v-if="condition.editing" @click="saveCondition(condition)">저장</v-btn>
          <v-btn v-if="condition.editing" @click="cancelCondition(condition)">취소</v-btn>
          </v-col>
          </v-row>
          <v-divider></v-divider>
        </div>
        <v-divider></v-divider>
        <v-row>
          <v-col><v-text-field value="신규등록" disabled label="일련번호"></v-text-field></v-col>
          <v-col>
            <v-select
              v-model="new_condition.con_type"
              :items="condition_type"
              item-text="text"
              item-value="value"
              label="조건 타입"
              persistent-hint
              return-object
              
            ></v-select>
          </v-col>
          <v-col>
            <v-select
              v-if="new_condition.con_type == 'FACTOR' || new_condition.con_type.value == 'FACTOR'"
              v-model="new_condition.con_value1"
              :items="factor_list"
              item-text="name"
              item-value="seq_factor"
              label="팩터 타입"
              persistent-hint
              return-object
              
            ></v-select>
            <v-select
              v-if="new_condition.con_type == 'CHAPTER_CLEAR' || new_condition.con_type.value == 'CHAPTER_CLEAR'"
              v-model="new_condition.con_value1"
              :items="chapter_list"
              item-text="seq_chapter"
              item-value="seq_chapter"
              label="챕터 타입"
              persistent-hint
              return-object
              
            ></v-select>
            <v-select
              v-if="new_condition.con_type.value == 'ENDING' || new_condition.con_type == 'ENDING'"
              v-model="new_condition.con_value1"
              :items="ending_list"
              item-text="name"
              item-value="id"
              label="엔딩 타입"
              persistent-hint
              return-object
              
            ></v-select>
          </v-col>
          <v-col>
            <v-text-field 
              v-if="new_condition.con_type.value == 'FACTOR' || new_condition.con_type == 'FACTOR'"
              v-model="new_condition.con_value2" 
              label="조건2"></v-text-field>
          </v-col>
          <v-col>
            <v-btn color="primary" dark class="mb-2" @click="registCondition">등록</v-btn>
          </v-col>
          </v-row>
      </v-card>
      </v-container>
    </v-dialog>
      <v-data-table
        :headers="headers"
        :items="side_chapter_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>챕터</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-btn color="primary" dark class="mb-2" @click="createNewItem">New Item</v-btn>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field :disabled="!new_side_chapter_item" v-model="editedItem.seq_chapter" label="회차"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      활성화 <v-switch v-model="editedItem.active_flag"></v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      제목 <v-text-field v-model="editedItem.title" label="제목"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      비용 <v-text-field type="number" v-model="editedItem.gem" label="비용"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      잠금 설명 <v-text-field v-model="editedItem.lock_desc" label="잠금 설명"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.condition="{ item }" >
          <v-btn @click="settingCondition(item)">조건 설정</v-btn>
        </template>
        <template v-slot:item.resource_list="{ item }" >
          <v-btn @click="showResourceList(item.seq_chapter)">{{item.resource_info.aos_count}} / {{item.resource_info.ios_count}}</v-btn>
        </template>
        <template v-slot:item.illust_list="{ item }" >
          <v-btn @click="showIllustList(item.seq_chapter)">{{item.resource_info.aos_resource}} / {{item.resource_info.ios_resource}}</v-btn>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
      <v-row>
        <v-col>
          <v-btn @click="export_file">사이드 챕터</v-btn>
          <v-file-input label="사이드 챕터" @change="import_file"></v-file-input>  
        </v-col>
        <v-col>
          <v-btn @click="export_condtion_list">조건 리스트</v-btn>
          <v-file-input label="사이드 챕터 조건" @change="import_condition"></v-file-input>  
          <v-alert v-if="error_file_item" type="error">{{error_file_item}}</v-alert>
          
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      error_file_item: "",
      aos_resource: false,
      resource_dialog: false,
      illust_dialog: false,
      new_side_chapter_item: false,
      new_condition: {con_type:""},
      selected_chapter_info: {},
      condition_list: [],
      editConditionDialog: false,
      side_chapter_list: [],
      ios_resource_list:[],
      aos_resource_list:[],
      ios_illust_list:[],
      aos_illust_list:[],
      seq_title: 0,
      dialog: false,
      condition_type: [
        { text: "엔딩획득", value: "ENDING"},
        { text: "챕터클리어", value: "CHAPTER_CLEAR"},
        { text: "팩터", value: "FACTOR"},
      ],
      headers: [
        { text: '회차', value: 'seq_chapter' },
        { text: '활성화', value: 'active_flag' },
        { text: '제목', value: 'title' },
        { text: '비용', value: 'gem' },
        { text: '잠금 설명', value: 'lock_desc' },
        { text: '조건 설정', value: 'condition', sortable: false },
        { text: '리소스 리스트', value: 'resource_list', sortable: false },
        { text: '일러스트 리스트', value: 'illust_list', sortable: false },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetch_side_chapter_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    check_chapter_list() {
      return side_chapter_list.length() > 0;
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_SIDE_CHAPTER_LIST',
      'INSERT_SIDE_CHAPTER',
      'UPDATE_SIDE_CHAPTER',
      'DELETE_SIDE_CHAPTER',
      'FETCH_SIDE_CHAPTER_CONDITION',
      'INSERT_SIDE_CHAPTER_CONDTION',
      'UPDATE_SIDE_CHAPTER_CONDITION',
      'DELETE_SIDE_CHAPTER_CONDITION',
      'FETCH_SIDE_CHAPTER_RESOURCE_LIST',
      'FETCH_SIDE_CHAPTER_ILLUST_LIST',
      'UPDATE_SIDE_CHAPTER_LIST',
      'UPDATE_SIDE_CHAPTER_CONDITION_LIST',
    ]),
    changeTest(condtion) {
      condtion.con_type = condtion.con_type_object.value;
      this.$forceUpdate();
    },
    async fetch_side_chapter_condition() {
      this.condition_list.forEach((item) => {
        item.seq_condition++;
      })
      if ( this.condition_list.length == 0 ) this.$forceUpdate();
      
      let body_data = await this.FETCH_SIDE_CHAPTER_CONDITION(this.selected_chapter_info)
      this.condition_list = body_data.condition_list;

      this.condition_list.forEach((item) => {
        if(item.con_type == "CHAPTER_CLEAR") {
          item.con_value1 = parseInt(item.con_value1);
        }
        else if(item.con_type == "FACTOR") {
          item.con_value1 = parseInt(item.con_value1);
        }
      })

      this.editConditionDialog = true    
    },
    showResourceList(seq_chapter) {
      this.FETCH_SIDE_CHAPTER_RESOURCE_LIST({seq_title: this.seq_title, seq_chapter})
        .then((body_data) => {
          this.aos_resource_list = body_data.aos_resource_list;
          this.ios_resource_list = body_data.ios_resource_list;
          this.aos_resource = true;
          this.resource_dialog = true;
        })
    },
    change_resource_type(type) {
      this.aos_resource = type == "aos";
    },
    showIllustList(seq_chapter) {
      this.FETCH_SIDE_CHAPTER_ILLUST_LIST({seq_title: this.seq_title, seq_chapter})
        .then((body_data) => {
          this.aos_illust_list = body_data.aos_illust_list;
          this.ios_illust_list = body_data.ios_illust_list;
          this.aos_resource = true;
          this.illust_dialog = true;
        }) 
    },
    async registCondition() {
      let side_chapter_info = {
        seq_title: this.seq_title, 
        seq_chapter: this.selected_chapter_info.seq_chapter,
      };

      if(this.new_condition.con_type.value == "FACTOR") {
        await this.INSERT_SIDE_CHAPTER_CONDTION({side_chapter_info, 
          side_chapter_condition_info: {
            con_type: this.new_condition.con_type.value,
            con_value1: this.new_condition.con_value1.seq_factor,
            con_value2: this.new_condition.con_value2}
          })
      }
      else if(this.new_condition.con_type.value == "ENDING") {
        await this.INSERT_SIDE_CHAPTER_CONDTION({side_chapter_info, 
          side_chapter_condition_info: {
            con_type: this.new_condition.con_type.value,
            con_value1: this.new_condition.con_value1.id}
        })
      }
      else if(this.new_condition.con_type.value == "CHAPTER_CLEAR") {
        await this.INSERT_SIDE_CHAPTER_CONDTION({side_chapter_info, 
          side_chapter_condition_info: {
            con_type: this.new_condition.con_type.value,
            con_value1: this.new_condition.con_value1.seq_chapter,
        }})
      }
      await this.fetch_side_chapter_condition();
      this.$forceUpdate();
    },
    async settingCondition(item) {
      this.selected_chapter_info = item;
      await this.fetch_side_chapter_condition();
    },
    async saveCondition(condition) {
      if(condition.con_type == "FACTOR" || condition.con_type.value == "FACTOR") {
        await this.UPDATE_SIDE_CHAPTER_CONDITION({ 
          side_chapter_condition_info: {
            seq_condition: condition.seq_condition,
            con_type: "FACTOR",
            con_value1: condition.con_value1.seq_factor,
            con_value2: condition.con_value2}
          })
      }
      else if(condition.con_type == "ENDING" || condition.con_type.value == "ENDING") {
        await this.UPDATE_SIDE_CHAPTER_CONDITION({ 
          side_chapter_condition_info: {
            seq_condition: condition.seq_condition,
            con_type: "ENDING",
            con_value1: condition.con_value1.id}
        })
      }
      else if(condition.con_type == "CHAPTER_CLEAR" || condition.con_type.value == "CHAPTER_CLEAR") {
        await this.UPDATE_SIDE_CHAPTER_CONDITION({ 
          side_chapter_condition_info: {
            seq_condition: condition.seq_condition,
            con_type: "CHAPTER_CLEAR",
            con_value1: condition.con_value1.seq_chapter,
        }})
      }
      condition.seq_condition = undefined;
      this.fetch_side_chapter_condition();
    },
    editCondition(condition) {
      condition.editing = true;
      this.$forceUpdate();
    },
    cancelCondition(condition) {
      condition.editing = false;
      this.$forceUpdate();
    },
    deleteCondition(condition) {
      if(!confirm("해당 조건을 삭제 하시겠습니까?")) return;
      this.DELETE_SIDE_CHAPTER_CONDITION(condition)
        .then(() => {
          this.fetch_side_chapter_condition();
        }) 
    },
    createNewItem() {
      this.new_side_chapter_item = true;
      this.editedItem = {}
      this.dialog = true
    },
    editItem (item) {
      this.new_side_chapter_item = false;
      this.editedIndex = this.side_chapter_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      this.DELETE_SIDE_CHAPTER(item).then(() => {
        this.fetch_side_chapter_list();
      })
    },
    close () {
      this.dialog = false
    },
    fetch_side_chapter_list() {
      this.FETCH_SIDE_CHAPTER_LIST(this.seq_title)
        .then((body_data) => {
          this.side_chapter_list = body_data.side_chapter_list;
          this.side_chapter_condition_list = body_data.side_chapter_condition_list;
          this.ending_list = body_data.ending_list;
          this.chapter_list = body_data.chapter_list;
          this.factor_list = body_data.factor_list;
          this.resource_list = body_data.resource_list;

          let arranged_resource = _.keyBy(this.resource_list, "seq_chapter");

          this.side_chapter_list.forEach((item) => {
            item.resource_info = arranged_resource[item.seq_chapter];
            if(!item.resource_info) item.resource_info = {}
          })

          this.s3_url = body_data.s3_url
        });
    },
    save () {
      this.editedItem.seq_title = this.seq_title;
      if( this.new_side_chapter_item) {
        this.INSERT_SIDE_CHAPTER(this.editedItem).then(() =>{
          this.fetch_side_chapter_list();
        }) 
      }
      else {
        this.UPDATE_SIDE_CHAPTER(this.editedItem).then(() =>{
          this.fetch_side_chapter_list();
        }) 
      }
      this.close()
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_SIDE_CHAPTER_LIST({side_chapter_list: json_object, seq_title: this.seq_title})
            .then(() => {
              this.fetch_side_chapter_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_file() {
        var ws = XLSX.utils.json_to_sheet(this.side_chapter_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"SideChapter");
        XLSX.writeFile(wb, `SideChapterInfo_${this.seq_title}.xlsx`);
    },
    import_condition(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_SIDE_CHAPTER_CONDITION_LIST({side_chapter_condition_list: json_object, seq_title: this.seq_title})
            .then((response_body) => {
              this.fetch_side_chapter_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_condtion_list() {
        var ws = XLSX.utils.json_to_sheet(this.side_chapter_condition_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"side_chapter_condition");
        XLSX.writeFile(wb, `SideChapterConditionInfo_${this.seq_title}.xlsx`);
    },
  }
};
</script>
