<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="roulette_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
        <v-alert v-if="isLive" type="info" dense>데이터 수정 -> 디자인 적용</v-alert>
          <v-toolbar flat color="white">
            <v-toolbar-title>룰렛</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark class="mb-2" @click="createRoulette">New Item</v-btn>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.reward_index" label="reward_index"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.reward_type" label="보상 타입"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.reward_desc" label="보상 내용"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.reward_value" label="보상 값"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.weight" label="가중치"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import config from '../../src/config/config';
export default {
  name: 'Roulette',
  data() {
    return {
      roulette_list: [],
      seq_title: 0,
      dialog: false,
      insert: false,
      headers: [
        { text: '보상 인덱스', value: 'reward_index' },
        { text: '보상 타입', value: 'reward_type' },
        { text: '보상 내용', value: 'reward_desc' },
        { text: '보상 값', value: 'reward_value' },
        { text: '가중치', value: 'weight' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetchRouletteList();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_ROULETTE_LIST',
      'UPDATE_ROULETTE',
      'DELETE_ROULETTE',
      'INSERT_ROULETTE'
    ]),
    async fetchRouletteList() {
      let result = await this.FETCH_ROULETTE_LIST(this.seq_title)
      this.roulette_list = result.roulette_list;
    },
    editItem (item) {
      this.editedIndex = this.roulette_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.insert = false;
      this.dialog = true
    },
    createRoulette() {
      this.insert = true;
      this.dialog = true;
      this.editedItem = {}
    },
    async deleteItem (item) {
      await this.DELETE_ROULETTE(item)
      this.fetchRouletteList();
    },
    close () {
      this.dialog = false
    },
    async save () {
      this.insert ? 
        await this.INSERT_ROULETTE(this.editedItem): 
        await this.UPDATE_ROULETTE(this.editedItem);
      await this.fetchRouletteList();
      this.close();
    },
  }
};
</script>
