<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-overlay :value="updating">
        {{updating}}
    </v-overlay>

    
    <v-overlay :value="uploading">
      <v-progress-circular
        v-if="this.insert_list_aos.length"
        :rotate="-90"
        :size="100"
        :width="15"
        :value="insert_progress"
        color="primary"
      >
        aos 신규 {{ insert_progress }}
      </v-progress-circular>

      <v-progress-circular
        v-if="this.update_list_aos.length"
        :rotate="-90"
        :size="100"
        :width="15"
        :value="update_progress"
        color="teal"
      >
        aos 업데이트 {{ update_progress }}
      </v-progress-circular>

      <v-progress-circular
        v-if="this.insert_list_ios.length"
        :rotate="-90"
        :size="100"
        :width="15"
        :value="insert_progress"
        color="primary"
      >
        ios 신규 {{ insert_progress }}
      </v-progress-circular>

      <v-progress-circular
        v-if="this.update_list_ios.length"
        :rotate="-90"
        :size="100"
        :width="15"
        :value="update_progress"
        color="teal"
      >
        업데이트 {{ update_progress }}
      </v-progress-circular>
    </v-overlay>
    <v-container>
    <v-row>
      <v-col cols=12 sm=12 md=12>
        <v-icon right>mdi-xml</v-icon>xml
        <v-icon right>mdi-music-note</v-icon>사운드
        <v-icon right>mdi-thumb-up-outline</v-icon>썸네일
        <v-icon right>mdi-format-title</v-icon>타이틀
        <v-icon right>mdi-image</v-icon>이미지
        <v-icon right>mdi-alpha-i-circle-outline</v-icon>일러스트
        <v-icon right>mdi-alpha-c-circle-outline</v-icon>Common
        <v-icon right>mdi-debug-step-into</v-icon>인트로
        <v-icon right>mdi-details</v-icon>디테일
        <v-icon right>mdi-source-branch</v-icon>브랜치
      </v-col>
    </v-row>
    </v-container>
    <v-row>
      <v-col>
        <v-container>
          <v-file-input multiple label="aos File input" @change="onFileUploadAos"></v-file-input>
        </v-container>
        <v-container v-if="update_list_aos.length">
          업데이트 리스트
          <v-chip v-for="item in update_list_aos" :key="item.id" color="blue">
            <v-avatar left class="blue darken-4">{{item.version}}</v-avatar>
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="insert_list_aos.length">
          신규 리스트
          <v-chip v-for="item in insert_list_aos" :key="item.id" color="green">
            <v-avatar left class="green darken-4">{{item.version}}</v-avatar>
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="invalid_list_aos.length">
          잘못된 파일 리스트
          <v-chip v-for="item in invalid_list_aos" :key="item.id" color="red">
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="crc_conflict_list_aos.length">
          crc_conflict 리스트
          <v-chip v-for="item in crc_conflict_list_aos" :key="item.id" color="dark gray">
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="update_list_aos.length || insert_list_aos.length">
          <v-btn @click="uploadFileAos">aos 업로드</v-btn>
        </v-container>
      </v-col>
      <v-col>
        <v-container>
          <v-file-input multiple label="ios File input" @change="onFileUploadIos"></v-file-input>
        </v-container>
        <v-container v-if="update_list_ios.length">
          업데이트 리스트
          <v-chip v-for="item in update_list_ios" :key="item.id" color="blue">
            <v-avatar left class="blue darken-4">{{item.version}}</v-avatar>
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="insert_list_ios.length">
          신규 리스트
          <v-chip v-for="item in insert_list_ios" :key="item.id" color="green">
            <v-avatar left class="green darken-4">{{item.version}}</v-avatar>
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="invalid_list_ios.length">
          잘못된 파일 리스트
          <v-chip v-for="item in invalid_list_ios" :key="item.id" color="red">
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="crc_conflict_list_ios.length">
          crc_conflict 리스트
          <v-chip v-for="item in crc_conflict_list_ios" :key="item.id" color="dark gray">
            {{item.id}}
            <v-icon right>{{item.vicon}}</v-icon>
          </v-chip>
        </v-container>
        <v-container v-if="update_list_ios.length || insert_list_ios.length">
          <v-btn @click="uploadFileIos">ios 업로드</v-btn>
        </v-container>
      </v-col>
    </v-row>
    
    <v-container>
    <v-row>
        <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
    </v-row>
    <v-row>
    </v-row>
    <v-row>
        <v-col cols="3">
          <v-select
            v-model="db_target"
            :items="target_list"
            item-text="name"
            item-value="value"
            label="db_target"
            persistent-hint
          ></v-select>
            <v-btn @click="update_live_design()">상용서버 업데이트</v-btn>
        </v-col>
    </v-row>
    </v-container>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex';
var crc = require('crc');
const {s3_upload, readFileAsync, exportTxt} = require("../util/fileutil");

var dateFormat = require('dateformat');
import XLSX from 'xlsx';

export default {
  name: 'Banner',
  data() {
    return {
      db_target: "qa",
      target_list:[
        {name: "상용", value: "live"},
      ],
      save_error: "",
      client_version_info: {},
      patch_version_info: {},
      product_arranged_map: null,
      image_url: "",
      image_file: {},
      insert: false,
      dialog: false,
      editedItem: {},
      uploading: "",
      updating: "",
      update_progress: 0,
      insert_progress: 0,
      update_list_aos: [],
      insert_list_aos: [],
      invalid_list_aos: [],
      crc_conflict_list_aos: [],
      update_list_ios: [],
      insert_list_ios: [],
      invalid_list_ios: [],
      crc_conflict_list_ios: [],
      resource_list_aos: [],
      resource_list_ios: [],
      arranged_resource_object_aos: null,
      arranged_resource_object_crc32_aos: null,
      arranged_resource_object_ios: null,
      arranged_resource_object_crc32_ios: null
    }
  },
  created() {
    this.fetch_live_system_info();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    ...mapState({
        CDN_URL: 'CDN_URL'
    }),
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_LIVE_SYSTEM_INFO',
      'UPDATE_CLIENT_VERSION',
      'UPDATE_PATCH_VERSION',
      'UPDATE_LIVE_RESOURCE',
      "PATCH_RESOURCE",
      "UPDATE_DESIGN"
    ]),
    get_product_name(seq_product) {
      let product_info = this.product_arranged_map[seq_product];
      return product_info ? product_info.product_name : ""
    },
    makeDate(date) {
      return dateFormat(date, 'yyyy-mm-dd HH:MM');
    },
    update_client_version() {
        this.UPDATE_CLIENT_VERSION(this.patch_version_info)
        .then(() => save_error = "")
        .catch((err) => {
          this.save_error = err;
          console.log(err);
        })
    },
    async patch_resource() {
        this.updating = "리소스 패치중";
        let result = await this.PATCH_RESOURCE();
        console.log(result);
        if(result.error) {
          this.save_error = result.error;
          this.updating = "리소스 패치중";
        }
        else {
          this.updating = "";
          this.fetch_system_info();
        }
    },
    async onFileUpload(file_list, os_type) {
      let arranged_resource_object_crc32 = null;
      let arranged_resource_object = null;

      if(os_type == "aos") {
        this.update_list_aos = [];
        this.insert_list_aos = [];
        this.invalid_list_aos = [];
        this.crc_conflict_list_aos = [];
        arranged_resource_object_crc32 = this.arranged_resource_object_crc32_aos;
        arranged_resource_object = this.arranged_resource_object_aos;
      }
      else {
        this.update_list_ios = [];
        this.insert_list_ios = [];
        this.invalid_list_ios = [];
        this.crc_conflict_list_ios = [];
        arranged_resource_object_crc32 = this.arranged_resource_object_crc32_ios;
        arranged_resource_object = this.arranged_resource_object_ios;
      }

      let temp_conflict_list = [];
      let temp_invalid_list = [];
      let temp_insert_list = [];
      let temp_update_list = [];
      for( var i in file_list) {
        let file_object = {}
        
        let file = file_list[i]
        file_object.file = file
        
        let id = file.name;
        file_object.id = id;

        let file_type = id.split("_")[0]
        
        if( file_type == "image") {file_object.type = "IMAGE"; file_object.vicon="mdi-image";}
        else if( file_type == "sound") {file_object.type = "SOUND"; file_object.vicon="mdi-music-note";}
        else if( file_type == "xml") {file_object.type = "XML"; file_object.vicon="mdi-xml";}
        else if( file_type == "illust") {file_object.type = "ILLUST"; file_object.vicon="mdi-alpha-i-circle-outline";}
        else if( file_type == "title") {file_object.type = "TITLE"; file_object.vicon="mdi-format-title";}
        else if( file_type == "common") {file_object.type = "COMMON"; file_object.vicon="mdi-alpha-c-circle-outline";}
        else if( file_type == "intro") {file_object.type = "INTRO"; file_object.vicon="mdi-debug-step-into";}
        else if( file_type == "detail") {file_object.type = "DETAIL"; file_object.vicon="mdi-details";}
        else if( file_type == "illustthumb") {file_object.type = "ILLUST_THUMB"; file_object.vicon="mdi-alpha-i-circle";}
        else if( file_type == "branch") {file_object.type = "BRANCH"; file_object.vicon="mdi-source-branch";}
        else {
          file_object.type = "INVALID_TYPE"
          file_object.vicon="mdi-alert-circle"
          temp_invalid_list.push(file_object);
          continue;
        }

        let file_buffer = await readFileAsync(file);
        file_object.crc32 = crc.crc32(file_buffer).toString(16);
        file_object.size = file.size;

        let crc_file = arranged_resource_object_crc32[file_object.crc32] 
        if (crc_file) {
          file_object.vicon = "mdi-emoticon-confused"
          temp_conflict_list.push(file_object)
          continue;
        }

        let update_file = arranged_resource_object[file_object.id];

        if(update_file) {
          file_object.version = update_file.version + 1
          temp_update_list.push(file_object);
          continue;
        }

        file_object.version = 1;
        temp_insert_list.push(file_object)
      }

      os_type == "aos" ? this.invalid_list_aos = temp_invalid_list: this.invalid_list_ios= temp_invalid_list
      os_type == "aos" ? this.insert_list_aos = temp_insert_list: this.insert_list_ios = temp_insert_list;
      os_type == "aos" ? this.update_list_aos  = temp_update_list: this.update_list_ios = temp_update_list;
      os_type == "aos" ? this.crc_conflict_list_aos  = temp_conflict_list: this.crc_conflict_list_ios = temp_conflict_list;
    },
    async onFileUploadAos(file_list) {
      await this.onFileUpload(file_list, "aos");
    },
    async onFileUploadIos(file_list) {
      await this.onFileUpload(file_list, "ios");
    },
    async uploadFile(os_type) {
      this.uploading = true;
      this.update_progress = 0;
      this.insert_progress = 0;
      let update_list = os_type == "aos" ? this.update_list_aos : this.update_list_ios;
      let insert_list = os_type == "aos" ? this.insert_list_aos : this.insert_list_ios;

      for(var i in update_list) {
        let update_file = update_list[i];
        await s3_upload(update_file.file, `${os_type}/${update_file.id}`)
        this.update_progress = parseInt(( parseInt(i) + 1 ) / update_list.length * 100);
      }

      for(var j in insert_list) {
        let insert_file = insert_list[j]
        await s3_upload(insert_file.file, `${os_type}/${insert_file.id}`)
        this.insert_progress = parseInt((parseInt(j) + 1) / insert_list.length * 100) ;
      }

      let result = await this.UPDATE_LIVE_RESOURCE({os_type, insert_list, update_list});
      exportTxt(result.purge_list.join("\r\n"), `PurgeResource_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.uploading = false;

    },
    async uploadFileAos() {
      await this.uploadFile("aos")
    },
    async uploadFileIos() {
      await this.uploadFile("ios")
    },
    async update_live_design() {
        let target_arranged_map = _.keyBy(this.target_list, "value")
        console.log(this.db_target);
        if(!confirm(`${target_arranged_map[this.db_target].name} 서버에 디비 패치를 하시겠습니까?`)) { return; }
        this.updating = "DB 업데이트 중";
        await this.UPDATE_DESIGN(this.db_target);
        this.updating = "";
    },
    fetch_live_system_info() {
      this.FETCH_LIVE_SYSTEM_INFO()
      .then((body_data) => {
          this.patch_version_info = body_data.patch_version_info;
          this.client_version_info = body_data.client_version_info;
          this.resource_list_aos = body_data.resource_list_aos;
          this.resource_list_ios = body_data.resource_list_ios;

          this.arranged_resource_object_aos =  _.keyBy(this.resource_list_aos, "id");
          this.arranged_resource_object_crc32_aos = _.keyBy(this.resource_list_aos, "crc32");

          this.arranged_resource_object_ios =  _.keyBy(this.resource_list_ios, "id");
          this.arranged_resource_object_crc32_ios = _.keyBy(this.resource_list_ios, "crc32");
      });
    },
    editItem (item) {
      this.insert = false;
      this.editedItem = Object.assign({}, item)
      this.image_file = {};
      this.image_url = `${this.CDN_URL}${this.editedItem.image}`;
      this.dialog = true
    },
    deleteItem (item) {
      
    },
    close () {
      this.dialog = false
    },
  }
};
</script>
