

<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-overlay :value="updating">
      {{updating}}
    </v-overlay>
    <v-container>
    <v-alert v-if="isLive" type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
    </v-container>
    <v-btn color="primary" dark class="mb-2" @click="onMetaTitleUpdate" v-if="!isLive">메타 타이틀 업데이트</v-btn>
    <v-btn color="primary" dark class="mb-2" @click="onLiveMetaTitleUpdate" v-if="isLive">메타 타이틀 업데이트(라이브)</v-btn>
      <v-tabs
        fixed-tabs
        background-color="indigo"
        dark
      >
        <v-tab v-for="tab_item in tabs" :key="tab_item.seq" @click.prevent="get_tab_link(tab_item)">
          {{tab_item.title}}
        </v-tab>
        
        
      </v-tabs>
      <router-view></router-view>
    </v-layout>
  </v-container>
</template>

<script>
import {mapActions} from 'vuex';
import config from '../../src/config/config';
var dateFormat = require('dateformat');
import _ from 'lodash';
const {s3_upload, readFileAsync, exportTxt, exportPurgeTxt} = require("../util/fileutil");

export default {
  name: '',
  created() {
    this.seq_title = this.$route.params.seq_title;
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  methods: {
    ...mapActions([
      'META_TITLE_UPDATE_SEQ_TITLE',
      'META_TITLE_UPDATE_SEQ_TITLE_LIVE',
      'OPERATE_SERVER'
    ]),
    get_tab_link(tab_item) {
      this.$router.push(`/title_info/${this.seq_title}/${tab_item.path}`);
    },

    async onLiveMetaTitleUpdate() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      const result = await this.META_TITLE_UPDATE_SEQ_TITLE_LIVE(this.seq_title);
      exportPurgeTxt(result.purge_list, `TitleMeta_${this.seq_title}_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      
      this.updating = ""
    },
    async onMetaTitleUpdate() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_TITLE_UPDATE_SEQ_TITLE(this.seq_title);
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
  },
  data() {
    return {
      updating: "",
      seq_title: "",
      tabs: [
        {type: "title_info", title: "타이틀", path:"title_info"},
        {type: "chapter", title: "챕터", path:"chapter_info"},
        {type: "side_chapter", title: "사이드 챕터", path:"side_chapter_info"},
        {type: "chapter", title: "상세", path:"chapter_detail"},
        {type: "root", title: "루트", path:"chapter_root"},
        {type: "ending", title: "엔딩", path:"ending_info"},
        {type: "character", title: "캐릭터", path:"character_info"},
        {type: "factor", title: "팩터", path:"factor_info"},
        {type: "item", title: "아이템", path:"payitem_info"},
        {type: "choice", title: "선택지", path:"choice_info"},
        {type: "costume", title: "의상", path:"costume_info"},
      ],
    }
  }
  ,
};
</script>
