<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="item_list"
        sort-by="calories"
        class="elevation-1"
        :search="search"
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>아이템</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              
              hide-details
            ></v-text-field>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.id" disabled label="아이템 아이디"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.name" disabled label="아이템 이름"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.type" disabled label="아이템 타입"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.price_type"
                        :items="price_type"
                        item-text="text"
                        item-value="value"
                        label="Select"
                        persistent-hint
                        return-object
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.price" label="가격"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'PayItemInfo',
  data() {
    return {
      search: '',
      item_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: '아이디', value: 'id' },
        { text: '아이템 이름', value: 'name' },
        { text: '아이템 타입', value: 'type' },
        { text: '재화 타입', value: 'price_type' },
        { text: '비용', value: 'price' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      price_type: [
        { text: "젬", value: "GEM"},
        { text: "에너지", value: "ENERGY"},
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    console.log(this.seq_title);
    this.FETCH_ITEM_LIST(this.seq_title)
      .then((body_data) => {this.item_list = body_data.item_list});
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_ITEM_LIST',
      'UPDATE_ITEM'
    ]),
    editItem (item) {
      this.editedIndex = this.item_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.item_list.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.item_list.splice(index, 1)
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      this.editedItem.seq_title = this.seq_title;
      this.editedItem.price_type = this.editedItem.price_type.value;
      this.UPDATE_ITEM(this.editedItem).then(() => {
          this.FETCH_ITEM_LIST(this.seq_title)
            .then((body_data) => {this.item_list = body_data.item_list});
          this.close()
      })
      
    },
  }
};
</script>
