import Vue from 'vue'
import VueRouter from 'vue-router'
import HelloWorld from '../components/HelloWorld.vue'
import TitleList from '../components/TitleList.vue'
import Login from '../components/Login.vue'
import SignIn from '../components/SignIn.vue'
import TitleInfo from '../components/TitleInfo.vue'
import ChapterInfo from '../components/ChapterInfo.vue'
import SideChapterInfo from '../components/SideChapterInfo.vue'
import EndingInfo from '../components/EndingInfo.vue'
import CharacterInfo from '../components/CharacterInfo.vue'
import FactorInfo from '../components/FactorInfo.vue'
import PayItemInfo from '../components/PayItemInfo.vue'
import ChoiceInfo from '../components/ChoiceInfo.vue'
import CostumeInfo from '../components/CostumeInfo.vue'
import PropInfo from '../components/PropInfo.vue'
import SystemPropInfo from '../components/SystemPropInfo.vue'

import ProductInfo from '../components/ProductInfo.vue'
import ProductAdultInfo from '../components/ProductAdultInfo.vue'
import ProductTabInfo from '../components/ProductTabInfo.vue'
import RouletteInfo from '../components/RouletteInfo.vue'
import GiftVideoInfo from '../components/GiftVideoInfo.vue'
import AttendanceInfo from '../components/AttendanceInfo.vue'
import ChapterDetailInfo from '../components/ChapterDetailInfo.vue'
import ChapterRootInfo from '../components/ChapterRootInfo.vue'
import RewardGroup from '../components/RewardGroup.vue'
import BannerInfo from '../components/BannerInfo.vue'
import NoticeInfo from '../components/NoticeInfo.vue'
import SaleEventInfo from '../components/SaleEventInfo.vue'
import AccessEventInfo from '../components/AccessEventInfo.vue'
import GmsNotice from '../components/GmsNotice.vue'

import IosUpload from '../components/IosUpload.vue'
import AosUpload from '../components/AosUpload.vue'

import MainCategory from '../components/MainCategory.vue'
import SubCategory from '../components/SubCategory.vue'

import TitleInfoTab from '../components/TitleInfoTab.vue'
import CategoryTab from '../components/CategoryTab.vue'
import TitleTicketCost from '../components/TitleTicketCost.vue'

import LobyInfo from '../components/LobbyInfo.vue'
import QurationInfo from '../components/QurationInfo.vue'
import MetaInfo from '../components/MetaInfo.vue'
import PatchService from '../components/PatchService.vue'
import PatchLiveService from '../components/PatchLiveService.vue'

import NotFound from '../components/NotFound.vue'

import store from '../store';

import DatetimePicker from 'vuetify-datetime-picker'
import 'vuetify-datetime-picker/src/stylus/main.styl'
import JsonExcel from 'vue-json-excel';

Vue.use(VueRouter);
Vue.use(DatetimePicker);

Vue.component('downloadExcel', JsonExcel);

const requireAuth = (to, from, next) => {
    const loginPath = `/login?rPath=${encodeURIComponent(to.path)}`;
    const { session_id, user_id } = localStorage;
    
    console.log(session_id, user_id);
    
    store.commit('LOGIN', { session_id, user_id } || {});
    store.getters.isAuth ? next() : next(loginPath);
}

const router = new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: HelloWorld,
            beforeEnter: requireAuth
        },
        {
            path: '/title_list',
            component: TitleList,
            beforeEnter: requireAuth,
        },
        {
            path: '/category_info',
            component: CategoryTab,
            beforeEnter: requireAuth,
            children: [
                { path: 'main_category', component: MainCategory },
                { path: 'sub_category', component: SubCategory }
            ]
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/sign_in',
            component: SignIn
        },
        {
            path: '/tab_test',
            component: TitleInfoTab,
            beforeEnter: requireAuth
        },
        {
            path: '/test',
            component: MetaInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/patch',
            component: PatchService,
            beforeEnter: requireAuth
        },
        {
            path: '/patch_live',
            component: PatchLiveService,
            beforeEnter: requireAuth
        },
        {
            path: '/prop_info',
            component: PropInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/system_prop_info',
            component: SystemPropInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/product_tab',
            component: ProductTabInfo,
            beforeEnter: requireAuth,
            children: [
                { path: 'product', component: ProductInfo },
                { path: 'product_adult', component: ProductAdultInfo }
            ]
        },
        {
            path: '/roulette_info',
            component: RouletteInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/gift_video_info',
            component: GiftVideoInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/ios_upload',
            component: IosUpload,
            beforeEnter: requireAuth
        },
        {
            path: '/aos_upload',
            component: AosUpload,
            beforeEnter: requireAuth
        },
        {
            path: '/lobby_info',
            component: LobyInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/quration',
            component: QurationInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/banner',
            component: BannerInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/notice',
            component: NoticeInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/sale_event',
            component: SaleEventInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/access_event/:server_type',
            component: AccessEventInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/reward_group',
            component: RewardGroup,
            beforeEnter: requireAuth
        },
        {
            path: '/title_ticket_cost',
            component: TitleTicketCost,
            beforeEnter: requireAuth
        },
        {
            path: '/attendance',
            component: AttendanceInfo,
            beforeEnter: requireAuth
        },
        {
            path: '/gms_notice',
            component: GmsNotice,
            beforeEnter: requireAuth
        },
        {
            path: '/title_info/:seq_title',
            component: TitleInfoTab,
            beforeEnter: requireAuth,
            children: [
                { path: 'title_info', component: TitleInfo },
                { path: 'chapter_info', component: ChapterInfo },
                { path: 'side_chapter_info', component: SideChapterInfo },
                { path: 'ending_info', component: EndingInfo },
                { path: 'character_info', component: CharacterInfo },
                { path: 'factor_info', component: FactorInfo },
                { path: 'payitem_info', component: PayItemInfo },
                { path: 'costume_info', component: CostumeInfo },
                { path: 'choice_info', component: ChoiceInfo },
                { path: 'chapter_detail', component: ChapterDetailInfo },
                { path: 'chapter_root', component: ChapterRootInfo }
            ]
        },
        {
            path: '*',
            component: NotFound
        }
    ]
});

export default router;