<template>
  <v-container>
    <v-overlay :value="updating">
      {{updating}}
    </v-overlay>
    <v-layout>
      <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
      <v-alert v-if="isLive" type="info" dense>데이터 수정 -> 디자인 적용</v-alert>
    </v-layout>
    <v-layout
      text-center
      wrap
    >
      

      <v-data-table
        :headers="headers"
        :items="system_prop_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>게임 상수</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled v-model="editedItem.PROP_KEY" label="PROP_KEY"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.PROP_VAL" label="PROP_VAL"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
      
      
    </v-layout>
  </v-container>
</template>
<script src="xlsx.full.min.js"></script>
<script src="js/jhxlsx.js"></script>

<script>
import XLSX from 'xlsx';
import { mapActions } from 'vuex';
import config from '../../src/config/config';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      updating: "",
      system_prop_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: 'PROP_KEY', value: 'PROP_KEY' },
        { text: 'PROP_VAL', value: 'PROP_VAL' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetch_prop_list();
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  watch: {
  },
  methods: {
    async meta_update() {
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    ...mapActions([
      'FETCH_SYSTEM_PROP_LIST',
      'UPDATE_SYSTEM_PROP',
      'OPERATE_SERVER'
    ]),
    fetch_prop_list() {
      this.FETCH_SYSTEM_PROP_LIST(this.seq_title)
        .then((body_data) => {this.system_prop_list = body_data.system_prop_list});
    },
    editItem (item) {
      this.editedIndex = this.system_prop_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      this.UPDATE_SYSTEM_PROP(this.editedItem).then(() => {
        this.close();
        this.fetch_prop_list();
      })
    },
  }
};
</script>
