<template>
  <v-container>
    <v-alert v-if="isLive" type="info" dense>데이터 수정 -> 디자인 적용</v-alert>    
    <v-btn @click="newMainTitle">
      New Item
    </v-btn>
    <v-dialog v-model="insertMainTitleDialog" max-width="500px">
      <v-card>
      <v-text-field v-model="edit_main_title.long_desc" label="긴 설명"></v-text-field>
      <v-text-field v-model="edit_main_title.short_desc" label="짧은 설명"></v-text-field>
      <v-radio-group v-model="edit_main_title.type" :mandatory="false" row>
        <v-radio label="랜덤" value="RANDOM"></v-radio>
        <v-radio label="순차" value="ASC"></v-radio>
      </v-radio-group>
      <v-radio-group v-model="edit_main_title.display_type" :mandatory="false" row>
        <v-radio label="썸네일" value="THUMB"></v-radio>
        <v-radio label="신규" value="NEW_IMAGE"></v-radio>
      </v-radio-group>
      <v-text-field type="number" v-model="edit_main_title.display_order" label="정렬 순서"></v-text-field>
      <v-btn @click="onSaveMainTitle">저장</v-btn>
      </v-card>
    </v-dialog>
    <v-dialog v-model="editLinkDialog" max-width="500px">
      <v-card>
      <v-text-field disabled v-model="edit_link_obj.desc" label="그룹명"></v-text-field>
      <v-text-field disabled v-model="edit_link_obj.title_name" label="타이틀명"></v-text-field>
      <v-text-field type="number" v-model="edit_link_obj.display_order" label="정렬 순서"></v-text-field>
      <v-btn @click="onSaveLink">저장</v-btn>
      <v-btn @click="onDeleteLink">링크 삭제</v-btn>
      </v-card>
    </v-dialog>
    <v-dialog v-model="insertLinkDialog" max-width="500px">
      <v-card>
      <v-text-field disabled v-model="insert_link_obj.long_desc" label="그룹명"></v-text-field>
      <v-select
        v-model="insert_link_obj.title_info"
        :items="title_list"
        item-text="name"
        item-value="seq_title"
        label="Select"
        persistent-hint
        return-object
        
      ></v-select>
      <v-text-field type="number" v-model="insert_link_obj.display_order" label="정렬 순서"></v-text-field>
      <v-btn :disabled="!(insert_link_obj.title_info && insert_link_obj.display_order)" @click="onInsertLink">추가</v-btn>
      </v-card>
    </v-dialog>
    <v-container>
      메인 그룹관리
      <v-row>
        <v-col>
          그룹 설명 문구
        </v-col>
        <v-col>
          활성화 여부
        </v-col>
        <v-col>
          그룹 표시 방식
        </v-col>
        <v-col>
          그룹 이미지
        </v-col>
        <v-col>
          타이틀 목록
        </v-col>
        <v-col>
          정렬순서
        </v-col>
        <v-col>
          편집
        </v-col>
      </v-row>
      <v-divider></v-divider>
      <v-container v-for="order_title in main_title" :key="order_title.order_main_title" name="temp">
      <v-row >
        <v-col>
          <v-text-field :disabled="!order_title.editing" v-model="order_title.long_desc" label="긴설명"></v-text-field>
          <v-text-field :disabled="!order_title.editing" v-model="order_title.short_desc" label="짧은 설명"></v-text-field>
        </v-col>
        <v-col>
          <v-radio-group :disabled="!order_title.editing" v-model="order_title.active_yn" :mandatory="false" row>
            <v-radio label="활성" value="Y"></v-radio>
            <v-radio label="비활성" value="N"></v-radio>
          </v-radio-group>
        </v-col>
        <v-col>
          <v-radio-group :disabled="!order_title.editing" v-model="order_title.type" :mandatory="false" row>
            <v-radio label="랜덤" value="RANDOM"></v-radio>
            <v-radio label="순차" value="ASC"></v-radio>
          </v-radio-group>
        </v-col>
        <v-col>
          <v-radio-group :disabled="!order_title.editing" v-model="order_title.display_type" :mandatory="false" row>
            <v-radio label="썸네일" value="THUMB"></v-radio>
            <v-radio label="신규" value="NEW_IMAGE"></v-radio>
          </v-radio-group>
        </v-col>
        <v-col>
          <div v-for="link in order_title.link_list" :key="link.seq_title">
          <v-chip close close-icon="mdi-pencil" @click:close="editLink(order_title, link)">
            <v-avatar left class="darken-4">{{link.display_order}}</v-avatar>
            {{link.name}}
          </v-chip>
          </div>
          <v-btn @click="link_title(order_title)">타이틀 추가</v-btn>
        </v-col>
        <v-col>
          <v-text-field :disabled="!order_title.editing" v-model="order_title.display_order" label="표시 순서"></v-text-field>
        </v-col>
        <v-col>
          <v-btn v-if="!order_title.editing" @click="onEdit(order_title)">편집</v-btn>
          <v-btn v-if="!order_title.editing" @click="onDelete(order_title)">삭제</v-btn>
          <v-btn v-if="order_title.editing" @click="onSave(order_title)">저장</v-btn>
          <v-btn v-if="order_title.editing" @click="onCancel">취소</v-btn>
        </v-col>
      </v-row>
      <v-divider></v-divider>
      </v-container>
    </v-container>    

  </v-container>
  
</template>

<script>
import config from '../../src/config/config';

import {auth, setAuthInHeader} from '../api'
import {mapActions} from 'vuex'

export default {
  name: 'HelloWorld',
  methods: {
    ...mapActions([
      'FETCH_LOBBY_INFO',
      'INSERT_LOBBY_MAIN',
      'UPDATE_LOBBY_MAIN',
      'DELETE_LOBBY_MAIN',
      'UPDATE_LINK',
      'LINK_LOBBY_MAIN',
      'UNLINK_LOBBY_MAIN',
    ]),
    onInsertLink() {
      this.insert_link_obj.seq_title = this.insert_link_obj.title_info.seq_title
      this.LINK_LOBBY_MAIN(this.insert_link_obj)
        .then(() => {
          this.insertLinkDialog = false;
          this.insert_link_obj = {};
          this.fetch_list();
        })
    },
    newMainTitle() {
      this.insertMainTitleDialog = true;
    },
    is_editable(order_title) {
      return order_title.editing;
    },
    link_title(order_title) {
      this.insert_link_obj.long_desc = order_title.long_desc;
      this.insert_link_obj.order_main_title = order_title.order_main_title;
      this.insertLinkDialog = true;
    },
    onSaveMainTitle() {
      this.INSERT_LOBBY_MAIN(this.edit_main_title).then(() => {
        this.fetch_list();
        this.insertMainTitleDialog = false;
      })
      
    },
    onSaveLink() {
      this.UPDATE_LINK(this.edit_link_obj).then(() => {
        this.fetch_list();
      })
      this.editLinkDialog = false;
    },
    onDeleteLink() {
      this.UNLINK_LOBBY_MAIN(this.edit_link_obj).then(() => {
        this.fetch_list();
      })
      this.editLinkDialog = false;
    },
    editLink(order_title, link) {

      this.edit_link_obj = Object.assign({}, {
        order_main_title: order_title.order_main_title,
        long_desc: order_title.long_desc,
        title_name: link.name,
        seq_title: link.seq_title,
        display_order: link.display_order,
      })

      console.log(order_title, link);
      this.editLinkDialog = true;
    },
    onEdit(order_title) {
      let edit_count = 0;
      this.main_title.forEach((item) => {
        if(item.editing) {
          edit_count++;
        }
      })

      if (edit_count > 0) {
        alert("현재 편집중인 그룹이 있습니다.")
        return;
      }

      order_title.editing = true;
      this.$forceUpdate();
    },
    onDelete(order_title) {
      if(order_title.order_main_title == 1) {
        alert("삭제 할 수 없습니다.")
      }

      if(!confirm("정말 삭제 하시겠습니까?"))  {
        return;
      }
      this.DELETE_LOBBY_MAIN(order_title)
        .then(() => this.fetch_list());
    
    },
    onSave(order_title) {
      this.UPDATE_LOBBY_MAIN(order_title)
        .then(() => {
          this.fetch_list();
        })      
    },
    onCancel() {
      this.fetch_list();
    },
    fetch_list() {
      this.FETCH_LOBBY_INFO()
      .then((body_data) => {
        this.title_list = {};
        this.main_title = {};
        this.main_title_link = {};
        
        this.title_list = body_data.title_list;
        this.main_title = body_data.main_title_list;
        this.main_title_link = body_data.main_title_link_list;

        this.arranged_main_title = _.keyBy(this.main_title, "order_main_title");
        this.arranged_title = _.keyBy(this.title_list, "seq_title");

        this.main_title_link.forEach((item) => {
          let main_title = this.arranged_main_title[item.order_main_title];
          if(!main_title.link_list) {
            main_title.link_list = [];
          }
          let title_info = this.arranged_title[item.seq_title];
          let link_object = Object.assign({}, {
            seq_title: title_info.seq_title,
            name: title_info.name,
            display_order: item.display_order
          })

          main_title.link_list.push(link_object);
        })

        this.main_title.forEach((item) => {
          item.link_list = _.orderBy(item.link_list, ['display_order'], ['asc']);
        });
      });
    }
    

  },
  watch: {
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  created() {
    this.fetch_list();
    
  },
  data() {
    return {
      title_list: [],
      insertLinkDialog: false,
      insert_link_obj: {},
      edit_main_title: {},
      insertMainTitleDialog: false,
      edit_link_obj: {},
      editLinkDialog: false,
      arranged_main_title: null,
      main_title: null,
      main_title_link: null,
      arranged_title: null,
      error: ""
    }
  }
  ,
};
</script>

<style>
</style>
