<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-dialog v-model="resource_dialog">
        <v-container>
        <v-card>
        <v-btn @click="change_resource_type('aos')">AOS</v-btn>
        <v-btn @click="change_resource_type('ios')">IOS</v-btn>
        <div v-if="aos_resource">
          <table>
            <tr v-for="item in aos_resource_list" :key="item.id">
              <td><a :href="`${s3_url}android/${item.url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        <div v-if="!aos_resource">
          <table>
            <tr v-for="item in ios_resource_list" :key="item.id">
              <td><a :href="`${s3_url}ios/${item.url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        </v-card>
        </v-container>
      </v-dialog>
      <v-dialog v-model="illust_dialog" max-width="500px">
        <v-container>
        <v-card>
        <v-btn @click="change_resource_type('aos')">AOS</v-btn>
        <v-btn @click="change_resource_type('ios')">IOS</v-btn>
        <div v-if="aos_resource">
          <table>
            <tr v-for="item in aos_illust_list" :key="item.id">
              <td><a :href="`${s3_url}android/${url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        <div v-if="!aos_resource">
          <table>
            <tr v-for="item in ios_illust_list" :key="item.id">
              <td><a :href="`${s3_url}ios/${url}`">{{item.id}}</a></td>
              <td>{{item.version}}</td>
              <td>{{item.type}}</td>
              <td>{{item.crc32}}</td>
              <td>{{item.update_date}}</td>
            </tr>
          </table>
        </div>
        
        </v-card>
        </v-container>
      </v-dialog>
      <v-data-table
        :headers="headers"
        :items="chapter_list"
        sort-by="calories"
        class="elevation-1"
      >
      

        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>챕터</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-btn color="primary" dark class="mb-2" @click="onCreate">New Item</v-btn>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.seq_chapter" label="회차" ref="seq_chapter"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      활성화 <v-switch v-model="editedItem.active_flag"></v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      최종화 여부 활성화 <v-switch v-model="editedItem.end_chapter"></v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      무료 <v-switch v-model="editedItem.free"></v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      기다리면 무료 <v-switch v-model="editedItem.wait_free"></v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      무료 대기 기간 <v-text-field v-model="editedItem.waiting_time" label="무료 대여 기간" ref="waiting_time"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      대여 기간 <v-text-field v-model="editedItem.rental_time" label="대여 기간" ref="rental_time"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      미리 보기 일시<v-datetime-picker v-model="editedItem.preview_start_date" ref="preview_start_date"> </v-datetime-picker>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      공개 일시<v-datetime-picker v-model="editedItem.publish_start_date" ref="publish_start_date"> </v-datetime-picker>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      비용 <v-text-field v-model="editedItem.rental_gem" label="대여 비용" ref="rental_gem"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      미리 보기 비용 <v-text-field v-model="editedItem.preview_rental_gem" label="대여 기간" ref="preview_rental_gem"></v-text-field>
                    </v-col>
                    
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
        <template v-slot:item.resource_cnt="{ item }">
          <v-btn @click="showResourceList(item.seq_chapter)">{{item.resource_info.aos_count}} / {{item.resource_info.ios_count}}</v-btn>
        </template>
        <template v-slot:item.illust_cnt="{item}">
          <v-btn @click="showIllustList(item.seq_chapter)">{{item.resource_info.aos_resource}} / {{item.resource_info.ios_resource}}</v-btn>
        </template>
      </v-data-table>
    </v-layout>
    <v-layout text-center wrap>
      <v-btn @click="export_file">파일 추출</v-btn>
      <v-file-input label="파일 입력" @change="import_file"></v-file-input>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
export default {
  name: 'ChapterInfo',
  data() {
    return {
      s3_url: "",
      resource_dialog: false,
      aos_resource: true,
      ios_resource_list: [],
      aos_resource_list: [],
      illust_dialog: false,
      aos_illust_list: [],
      ios_illust_list: [],
      chapter_list: [],
      seq_title: 0,
      dialog: false,
      save_error: "",
      headers: [
        { text: '회차', value: 'seq_chapter' },
        { text: '활성화', value: 'active_flag' },
        { text: '최종회차 여부', value: 'end_chapter' },
        { text: '무료', value: 'free' },
        { text: '기다리면 무료', value: 'wait_free' },
        { text: '무료 대기 시간', value: 'waiting_time' },
        { text: '대여 기간', value: 'rental_time' },
        { text: '미리 보기 일시', value: 'preview_start_date' },
        { text: '공개 일시', value: 'publish_start_date' },
        { text: '비용', value: 'rental_gem' },
        { text: '미리 보기 비용', value: 'preview_rental_gem' },
        { text: '리소스 개수', value: 'resource_cnt' },
        { text: '일러스트 개수', value: 'illust_cnt' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  
  created() {
    this.seq_title = this.$route.params.seq_title;
    this.fetch_chapter_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    check_chapter_list() {
      return chapter_list.length() > 0;
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_CHAPTER_LIST',
      'INSERT_CHAPTER',
      'UPDATE_CHAPTER',
      'UPDATE_CHAPTER_LIST',
      'DELETE_CHAPTER',
      'FETCH_CHAPTER_RESOURCE_LIST',
      'FETCH_CHAPTER_ILLUST_LIST',
    ]),
    change_resource_type(type) {
      this.aos_resource = type == "aos";
    },
    showResourceList(seq_chapter) {
      this.FETCH_CHAPTER_RESOURCE_LIST({seq_title: this.seq_title, seq_chapter})
        .then((body_data) => {
          this.aos_resource_list = body_data.aos_resource_list;
          this.ios_resource_list = body_data.ios_resource_list;
          this.aos_resource = true;
          this.resource_dialog = true;
        })
    },
    showIllustList(seq_chapter) {
      this.FETCH_CHAPTER_ILLUST_LIST({seq_title: this.seq_title, seq_chapter})
        .then((body_data) => {
          this.aos_illust_list = body_data.aos_illust_list;
          this.ios_illust_list = body_data.ios_illust_list;
          this.aos_resource = true;
          this.illust_dialog = true;
        })
    },
    fetch_chapter_list() {
      this.FETCH_CHAPTER_LIST(this.seq_title)
        .then((body_data) => {
          this.s3_url = body_data.s3_url;
          this.chapter_list = body_data.chapter_list,
          this.resource_list = body_data.resource_list
          let arranged_resource = _.keyBy(this.resource_list, "seq_chapter");

          this.chapter_list.forEach((item) => {
            item.resource_info = arranged_resource[item.seq_chapter];
            if(!item.resource_info) item.resource_info = {}
          })
        });
    },
    editItem (item) {
      this.save_error = "";
      this.editedIndex = this.chapter_list.indexOf(item)
      this.editedItem = Object.assign({}, item)


      this.editedItem.free = item.free == "Y" 
      this.editedItem.active_flag = item.active_flag == "Y" 
      this.editedItem.wait_free = item.wait_free == "Y"
      this.editedItem.end_chapter = item.end_chapter == "Y"
      this.is_edit = true;
      this.dialog = true
    },
    deleteItem (item) {
      if(!confirm('해당 챕터를 삭제 하시겠습니까?')) return;

      this.DELETE_CHAPTER(item)
        .then(() => {
          const index = this.chapter_list.indexOf(item)
          this.chapter_list.splice(index, 1)
        })
      
    },
    close () {
      
      this.dialog = false
    },
    check_null_value(value, ref, message) {
      if(!value) {
          this.$nextTick(() => ref.focus())
          this.save_error = message;  
          return false;
        }
        return true;
    },
    save () {
      if(!this.check_null_value(this.editedItem.seq_chapter, this.$refs.seq_chapter, "회차를 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.preview_start_date, this.$refs.preview_start_date, "미리 보기 시작을 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.publish_start_date, this.$refs.publish_start_date, "공개 시작을 입력해주세요")) return;

      this.editedItem.seq_title = this.seq_title;
      if(this.is_edit) {
        this.editedItem.free = this.editedItem.free ? "Y" : "N";
        this.editedItem.active_flag = this.editedItem.active_flag ? "Y" : "N";
        this.editedItem.wait_free = this.editedItem.wait_free ? "Y" : "N";
        this.editedItem.end_chapter = this.editedItem.end_chapter ? "Y" : "N";

        this.UPDATE_CHAPTER(this.editedItem).then(() =>{
          this.close();
          this.fetch_chapter_list()
        })
        .catch((err) => {
          this.save_error = err;
        }) 
      }
      else {
        this.INSERT_CHAPTER(this.editedItem).then(() =>{
          this.close();
          this.fetch_chapter_list()
        })
        .catch((err) => {
          this.save_error = err;
        }) 
      }
      
      
      
    },
    onCreate() {
      this.save_error = "";
      this.dialog = true
      this.is_edit=false;
      this.editedItem = {
        waiting_time: 0,
        rental_time: 0,
        rental_gem: 0,
        preview_rental_gem: 0
      }
      this.$refs.preview_start_date.date = "";
      this.$refs.preview_start_date.time = "";

      this.$refs.publish_start_date.date = "";
      this.$refs.publish_start_date.time = "";

      this.editedItem.publish_start_date = "";
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_CHAPTER_LIST({chapter_list: json_object, seq_title: this.seq_title})
            .then(() => {
              this.fetch_chapter_list();
            });
        })
      };

      reader.onerror = function(ex) {
          console.log(ex);
      };

      reader.readAsBinaryString(file);
    },
    export_file() {
        let temp_chapter_info = [...this.chapter_list];
        temp_chapter_info.forEach((item) => {
          delete item.resource_info;
        })

        var ws = XLSX.utils.json_to_sheet(temp_chapter_info);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"chapter_info");
        XLSX.writeFile(wb, `Chapter_info_${this.seq_title}.xlsx`);
    },
  }
};
</script>
