<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-data-table
      :headers="headers"
      :items="category_list"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>메인 태그</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark class="mb-2" @click="onCreate">New Item</v-btn>
        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.seq_category" disabled label="일련번호"></v-text-field>
                  </v-col>
                  
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="이름"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    활성화 여부<v-switch v-model="active_yn"></v-switch>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.display_order" label="정렬 순서"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    서브 메뉴<v-switch v-model="sub_main_yn"></v-switch>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-color-picker v-model="editedItem.background_color"></v-color-picker>
                  </v-col>
                </v-row>

              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.chip="{ item }">
        <v-chip :color="item.background_color">{{item.name}}</v-chip>
      </template>
      <template v-slot:item.action="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>
        
      </template>
    </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      insert: false,
      category_list: [],
      seq_title: 0,
      active_yn: false,
      sub_main_yn: false,
      dialog: false,
      headers: [
        { text: '카테고리 일련번호', value: 'seq_category' },
        { text: '칩', value: 'chip' },
        { text: '활성화 여부', value: 'active_yn' },
        { text: '정렬 순서', value: 'display_order' },
        { text: '서브 카테고리', value: '' },// 메인이지만 태그로 표시 안됨 
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetch_category_list();
    
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_CATEGORY_LIST',
      'UPDATE_TITLE_CATEGORY',
      'INSERT_TITLE_CATEGORY',
    ]),
    fetch_category_list() {
      this.FETCH_CATEGORY_LIST(this.seq_title)
        .then((body_data) => {
          let main_category_list = [];
          body_data.category_list.forEach((item) => {
            if( item.main_yn == "Y" ) {
              main_category_list.push(item);
            }
        })

        this.category_list = main_category_list;
      });
    },
    editItem (item) {
      this.insert = false;
      this.editedIndex = this.category_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.active_yn = this.editedItem.active_yn == "Y";
      this.sub_main_yn = this.editedItem.sub_main_yn == "Y";
      this.dialog = true
      
    },
    onCreate() {
      this.insert = true;
      this.dialog = true;
    },
    deleteItem (item) {
      
    },
    close () {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      this.editedItem.active_yn = this.active_yn ? "Y" : "N";
      this.editedItem.sub_main_yn = this.sub_main_yn ? "Y" : "N";
      this.editedItem.main_yn = "Y";

      if(this.insert) {
        this.INSERT_TITLE_CATEGORY(this.editedItem)
          .then(() => {
            this.fetch_category_list();
            this.dialog = false;
          })
      }
      else {
        this.UPDATE_TITLE_CATEGORY(this.editedItem)
          .then(() => {
            this.fetch_category_list();
            this.dialog = false;
          })
      }
      
      
    },
  }
};
</script>
