<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
      <v-container>
        <v-alert v-if="isLive" type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
      </v-container>
      <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
      <v-btn v-if="isLive" color="primary" dark class="mb-2" @click="meta_update_live">메타 파일 서버 적용(Live)</v-btn>
      <v-tabs
        fixed-tabs
        background-color="indigo"
        dark
      >
        <v-tab v-for="tab_item in tabs" :key="tab_item.seq" @click.prevent="get_tab_link(tab_item)">
          {{tab_item.title}}
        </v-tab>
      </v-tabs>
      <router-view></router-view>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import config from '../../src/config/config';
var dateFormat = require('dateformat');
const {s3_upload, readFileAsync, exportTxt, exportPurgeTxt} = require("../util/fileutil");
export default {
  name: '',
  created() {
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  methods: {
    ...mapActions([
      'META_CATEGORY_UPDATE',
      'META_CATEGORY_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    get_tab_link(tab_item) {
      this.$router.push(`/category_info/${tab_item.path}`);
    },
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_CATEGORY_UPDATE();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      let result = await this.META_CATEGORY_UPDATE_LIVE();
      this.updating = ""
      exportPurgeTxt(result.purge_list, `CategoryAndTag_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
    },
  },
  data() {
    return {
      updating: "",
      seq_title: "",
      tabs: [
        {type: "main_category", title: "메인 카테고리", path:"main_category"},
        {type: "sub_category", title: "서브 카테고리", path:"sub_category"},
      ],
    }
  }
};
</script>
