<template>
  <v-container>
    <v-overlay :value="updating">
        {{updating}}
    </v-overlay>
    <v-container v-if="!isLive"> 
      <v-row>
        <v-btn @click="meta_server_desc">server_desc</v-btn>
      </v-row>
      <v-row>
        <v-btn @click="download_log_asset">로그 명세</v-btn>
      </v-row>
    </v-container>
    <v-container v-else>
      <v-alert type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
      <v-row>
        <v-btn @click="meta_server_desc_live">server_desc(Live)</v-btn>
      </v-row>
    </v-container>

    <v-layout text-center wrap>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
import config from '../../src/config/config';
import _ from 'lodash';

const dateFormat = require('dateformat');
const {exportPurgeTxt} = require("../util/fileutil");


export default {
  name: 'SideChapterInfo',
  data() {
    return {
      updating: '',
      prop_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: 'prop_key', value: 'prop_key' },
        { text: 'prop_val', value: 'prop_val' },
        { text: 'prop_desc', value: 'prop_desc' },
        { text: 'prop_level', value: 'prop_level' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    this.fetch_prop_list();
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  watch: {
  },
  methods: {
    ...mapActions([
      'FETCH_PROP_LIST',
      'UPDATE_PROP',
      'META_BANNER_UPDATE',
      'META_SERVER_DESC_UPDATE',
      'META_SERVER_DESC_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    async meta_server_desc() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_SERVER_DESC_UPDATE();
      this.restartServer();
    },
    async meta_server_desc_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요";
      let result = await this.META_SERVER_DESC_UPDATE_LIVE();
      exportPurgeTxt(result.purge_list, `ServerDesc_${this.seq_title}_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = "";
    },
    async restartServer() {
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    } ,
    download_log_asset() {
      let grade_list = [];
      this.choice_list.forEach((item) => {
        let split_list = item.target_choice.split("_");
        let grade = split_list[split_list.length - 1];
        let grade_name = `${grade} 선택지`;
        if(grade.length == 0) return;
        if(parseInt(grade) <= 0) return;
        grade_list.push({grade, grade_name});
      })

      grade_list = _.uniqBy(grade_list, function (e) {
        return e.grade;
      });

      var ws = XLSX.utils.json_to_sheet(grade_list);
      let wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"스코어 로그 grade");
      XLSX.writeFile(wb, `스코어 로그 grade.xlsx`);

      let mode_list = [];
      this.title_list.forEach((item) => {
        mode_list.push({mode_id: item.seq_title, mode_name: item.name});
      })

      ws = XLSX.utils.json_to_sheet(mode_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"스코어 로그 mode");
      XLSX.writeFile(wb, `스코어 로그 mode.xlsx`);

      let sub_mode_list = [];
      this.chapter_list.forEach((item) => {
        sub_mode_list.push({submode_id: item.seq_chapter, submode_name: `${item.seq_chapter} 화`});
      });

      sub_mode_list = _.uniqBy(sub_mode_list, function (e) {
        return e.submode_id;
      });

      ws = XLSX.utils.json_to_sheet(sub_mode_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"스코어 로그 sub_mode");
      XLSX.writeFile(wb, `스코어 로그 sub_mode.xlsx`);

      let item_id_list = [];
      let title_arranged_map = _.keyBy(this.title_list, 'seq_title');
      this.chapter_list.forEach((item) => {
        let item_id = '1' + String(item.seq_title).padStart(3, 0) + String(item.seq_chapter).padStart(3, 0);
        let item_name = `${title_arranged_map[item.seq_title].name} ${item.seq_chapter} 화 대여`;

        item_id_list.push({item_id, item_name})
      });
      
      this.title_list.forEach((item) => {
        item_id_list.push({item_id : 10000000 + parseInt(item.seq_title), item_name: `${item.name} 소장권`})
        item_id_list.push({item_id : 10001000 + parseInt(item.seq_title), item_name: `${item.name} 보너스 소장권`})
        item_id_list.push({item_id : 10002000 + parseInt(item.seq_title), item_name: `${item.name} 대여권`})
        item_id_list.push({item_id : 10003000 + parseInt(item.seq_title), item_name: `${item.name} 보너스 대여권`})
      });
      
      
      ws = XLSX.utils.json_to_sheet(item_id_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"아이템 명세");
      XLSX.writeFile(wb, `아이템 명세.xlsx`);

      ws = XLSX.utils.json_to_sheet(this.title_item_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"타이틀 아이템");
      XLSX.writeFile(wb, `타이틀 아이템.xlsx`);

      let ret_action_list = [];
      action_list.forEach((item) =>{
        ret_action_list.push({
          action_id: item.action_id,
          action_name: item.action_name,
          action: item.action,
          })
      })

      ws = XLSX.utils.json_to_sheet(ret_action_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"재화 로그 액션 명세");
      XLSX.writeFile(wb, `재화 로그 액션 명세.xlsx`);

      ws = XLSX.utils.json_to_sheet(asset_list);
      wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws,"재화 로그 어셋 명세");
      XLSX.writeFile(wb, `재화 로그 어셋 명세.xlsx`);

      
    },
    fetch_prop_list() {
      this.FETCH_PROP_LIST(this.seq_title)
        .then((body_data) => {
          this.prop_list = body_data.prop_list;
          this.title_list = body_data.title_list;
          this.title_item_list = body_data.title_item_list;
          this.choice_list = body_data.title_choice_list;
          this.chapter_list = body_data.chapter_list;
        });
    },
    editItem (item) {
      this.editedIndex = this.prop_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      this.UPDATE_PROP(this.editedItem).then(() => {
        this.close();
        this.fetch_prop_list();
      })
    },
  }
};

let asset_list = [
  {asset_id: 1 , asset_name: "젬"},
  {asset_id: 2 , asset_name: "무료 젬"},
  {asset_id: 3 , asset_name: "에너지"}
]

let action_list = [
    { action_id: 1001, action_name: '가입 획득', action: 1 },
    { action_id: 1002, action_name: '상점 구매', action: 1 },
    { action_id: 1003, action_name: '룰렛 획득', action: 1 },
    { action_id: 1004, action_name: '동영상 시청 획득', action: 1 },
    { action_id: 1005, action_name: '출석 획득', action: 1 },
    { action_id: 1006, action_name: '우편 획득', action: 1 },
    { action_id: 1007, action_name: '일일 충전', action: 1 },
    { action_id: 1008, action_name: '구글 광고', action: 1 },
    { action_id: 1009, action_name: 'TNK 광고', action: 1 },
    { action_id: 1010, action_name: 'IGA 광고', action: 1 },
    { action_id: 1011, action_name: '교환 획득', action: 1 },
    { action_id: 1012, action_name: '운영 획득', action: 1 },
    { action_id: 1013, action_name: '이벤트 획득', action: 1 },
    { action_id: 1014, action_name: '에너지 교환 획득', action: 1 },
    { action_id: 1015, action_name: '접속 이벤트', action: 1 },
    { action_id: 1016, action_name: '월간 구독 지급', action: 1 },
    { action_id: 1017, action_name: '무료 우편함 동영상', action: 1 },
    { action_id: 1018, action_name: '스토리 무료 젬', action: 1 },
    { action_id: 1019, action_name: '치트 획득', action: 1 },
    { action_id: 1020, action_name: '타이틀 대여권 구매', action: 1 },
    { action_id: 1021, action_name: '타이틀 소장권 구매', action: 1 },
    { action_id: 2001, action_name: '의상 구매', action: 2, type: 'PURCHASE_DRESS' },
    { action_id: 2002, action_name: '선택지 구매', action: 2, type: 'PURCHASE_CHOICE' },
    { action_id: 2003, action_name: '이름 변경', action: 2 },
    { action_id: 2004, action_name: '챕터 대여', action: 2, type: 'CR' },
    { action_id: 2005, action_name: '에너지 교환 구매', action: 2 },
    { action_id: 2006, action_name: '타이틀 전체 대여', action: 2 },
    { action_id: 2007, action_name: '미리보기 챕터 대여', action: 2, type: 'PCR' },
    { action_id: 2008, action_name: '타이틀 전체 소장', action: 2, type: 'TP' },
    { action_id: 2009, action_name: '사이드 챕터 구매', action: 2, type: 'SCR' },
    { action_id: 2010, action_name: '교환 소모', action: 2 },
    { action_id: 2011, action_name: '상점 구매', action: 2 },
    { action_id: 2012, action_name: '타이틀 대여권 구매', action: 2 },
    { action_id: 2013, action_name: '타이틀 소장권 구매', action: 2 },
    { action_id: 2014, action_name: '치트 소비', action: 2 },
    { action_id: 2015, action_name: '무료 젬 만료', action: 2 },
    { action_id: 2016, action_name: '챕터 소장', action: 2, type: 'CP' },
    { action_id: 2017, action_name: '운영 회수', action: 2 },
]
</script>
