<template>
  <v-container>
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
      <v-container>
        <v-alert v-if="isLive" type="info" dense> 데이터 수정 -> 디자인 적용</v-alert>    
      </v-container>
      <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="notice_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>공지</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-btn color="primary" dark class="mb-2" @click="createNewBanner">New Item</v-btn>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" >
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled v-model="editedItem.seq_notice" label="일련번호"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.tag_type"
                        :items="tag_type_list"
                        item-text="text"
                        item-value="value"
                        label="태그 타입"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.title" label="제목"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.event_flag"
                        :items="active_type_list"
                        item-text="text"
                        item-value="value"
                        label="이벤트 여부"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field 
                        v-model="editedItem.event_start_date" 
                        label="이벤트 시작" 
                        :placeholder="makeDate(editedItem.event_start_date)"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field 
                        v-model="editedItem.event_end_date" 
                        label="이벤트 종료" 
                        :placeholder="makeDate(editedItem.event_end_date)"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field 
                        v-model="editedItem.notice_end_date" 
                        label="이벤트 종료" 
                        :placeholder="makeDate(editedItem.notice_end_date)"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.seq_title"
                        :items="title_list"
                        item-text="name"
                        item-value="seq_title"
                        label="연결 타이틀"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.notice_flag"
                        :items="active_type_list"
                        item-text="text"
                        item-value="value"
                        label="공지 플래그"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.popup_flag"
                        :items="active_type_list"
                        item-text="text"
                        item-value="value"
                        label="팝업 여부"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.active_flag"
                        :items="active_type_list"
                        item-text="text"
                        item-value="value"
                        label="활성화 여부"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.display_order" label="표시 순서"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.url_link" label="url_link"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.seq_product"
                        :items="product_list"
                        item-text="product_name"
                        item-value="seq_product"
                        label="연결 상품"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12">
                      <v-textarea v-model="editedItem.contents" label="내용"></v-textarea>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <img v-if="image_url" :src="image_url" width="212"/>
                      <v-file-input accept="image/*" label="배너 이미지" @change="onImageChange"></v-file-input>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.image="{ item }">
          <img v-if="item.image" :src="get_image_url(item)" width="250"/>
        </template>
        <template v-slot:item.product_info="{ item }">
          {{get_product_name(item.seq_product)}}
        </template>
        
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
      <v-row>
        <v-col cols="12" sm="2" md="2">
          <v-btn @click="export_file">공지 데이터 추출</v-btn>
        </v-col>
        <v-col cols="12" sm="2" md="2">
          <v-file-input label="배너" @change="import_file">공지 데이터</v-file-input>  
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import config from '../../src/config/config';
const dateFormat = require('dateformat');
const crc = require('crc');
const {s3_upload, exportPurgeTxt} = require("../util/fileutil");

import XLSX from 'xlsx';

export default {
  name: 'Banner',
  data() {
    return {
      updating: "",
      product_arranged_map: null,
      image_url: "",
      image_file: {},
      insert: false,
      title_list: [],
      notice_list: [],
      product_list: [],
      active_type_list: [
        {text:'활성', value: 'Y'},
        {text:'비활성', value: 'N'},
      ],
      tag_type_list: [
        {text:'공지', value: 'NOTICE'},
        {text:'사전예약', value: 'RESERVE'},
        {text:'이벤트', value: 'EVENT'},
        {text:'프로모션', value: 'PROMOTION'},
      ],
      dialog: false,
      headers: [
        { text: '공지 일련번호', value: 'seq_notice' },
        { text: '태그 타입', value: 'tag_type' },
        { text: '제목', value: 'title' },
        { text: '이미지', value: 'image' },
        { text: '이벤트 여부', value: 'event_flag' },
        { text: '이벤트 시작', value: 'event_start_date' },
        { text: '이벤트 종료', value: 'event_end_date' },
        { text: '공지 종료', value: 'notice_end_date' },
        { text: '상품 정보', value: 'product_info' },
        { text: '공지 여부', value: 'notice_flag' },
        { text: '팝업 여부', value: 'popup_flag' },
        { text: '표시 순서', value: 'display_order' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.fetch_notice_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    ...mapState({
        CDN_URL: 'CDN_URL'
    }),
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_NOTICE_LIST',
      'UPDATE_NOTICE',
      'INSERT_NOTICE',
      'UPDATE_NOTICE_INFO_LIST',
      'DELETE_NOTICE',
      'META_NOTICE_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    
    get_product_name(seq_product) {
      let product_info = this.product_arranged_map[seq_product];
      return product_info ? product_info.product_name : ""
    },
    makeDate(date) {
      return dateFormat(date, 'yyyy-mm-dd HH:MM');
    },
    onImageChange(upload_file) {
      if(!upload_file) {
        this.image_file = {};
        return;
      }
      
      upload_file.arrayBuffer().then((data) => {
        this.image_file.file = upload_file;
        this.image_file.crc = crc.crc32(data).toString(16);
        this.image_file.thumb = URL.createObjectURL(upload_file);
        this.image_url = URL.createObjectURL(upload_file);
      }) 
    },
    get_image_url(item) {
      return `${this.CDN_URL}${item.image}`;
    },
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    fetch_notice_list() {
      this.FETCH_NOTICE_LIST()
      .then((body_data) => {
        console.log(body_data);
        this.notice_list = body_data.notice_list;
        this.title_list = body_data.title_list;
        this.patch_info = body_data.patch_info;
        this.product_list = body_data.product_list;
        this.product_arranged_map =  _.keyBy(this.product_list, "seq_product")
        console.log(this.product_list);
      });
    },
    editItem (item) {
      this.insert = false;
      this.editedItem = Object.assign({}, item)
      this.image_file = {};
      this.image_url = `${this.CDN_URL}${this.editedItem.image}`;
      this.dialog = true
    },
    deleteItem (item) {
      this.DELETE_NOTICE(item).then(() => {
        this.fetch_notice_list();
      })
    },
    close () {
      this.dialog = false
    },
    createNewBanner() {
      this.editedItem = {}
      this.image_file = {};
      this.image_url = ''
      this.insert = true;
      this.dialog = true;
    },
    save () {
      if(this.image_file && this.image_file.file) this.editedItem.image = `images/${this.patch_info}/${this.image_file.file.name}`
      
      if(this.insert) {
        this.INSERT_NOTICE(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
          
            this.fetch_notice_list();
            this.close();
          })
      }
      else {
        this.UPDATE_NOTICE(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);

            this.fetch_notice_list();
            this.close();
          })
      }
      
      
      
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_NOTICE_INFO_LIST({notice_list: json_object})
            .then(() => {
              this.fetch_notice_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_file() {
        var ws = XLSX.utils.json_to_sheet(this.notice_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"notice");
        XLSX.writeFile(wb, 'NOTICE.xlsx');
    },
  }
};
</script>
