<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
    <v-overlay :value="updating">
        {{updating}}
    </v-overlay>

    <v-container>
    <v-row>
        <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
    </v-row>
    <v-row>
    </v-row>
    <v-row>
        <v-col cols="3">
            <v-text-field width="10" disabled v-model="patch_version_info" label="현재 버젼"></v-text-field>
        </v-col>
        <v-col cols="3">
            <v-btn @click="patch_resource()">패치 시작</v-btn>
        </v-col>
        <v-col cols="3">
          <v-select
            v-model="db_target"
            :items="target_list"
            item-text="name"
            item-value="value"
            label="db_target"
            persistent-hint
          ></v-select>
            <v-btn @click="update_design()">디자인 업데이트</v-btn>
        </v-col>
    </v-row>
    </v-container>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex';
var crc = require('crc');
const {s3_upload} = require("../util/fileutil");
var dateFormat = require('dateformat');
import XLSX from 'xlsx';

export default {
  name: 'Banner',
  data() {
    return {
      db_target: "qa",
      target_list:[
        {name: "테스트", value: "local"},
        {name: "상용", value: "proc"},
        {name: "QA 서버", value: "qa"},
        {name: "IOS 검수", value: "staging"},
      ],
      save_error: "",
      client_version_info: {},
      patch_version_info: {},
      product_arranged_map: null,
      image_url: "",
      image_file: {},
      insert: false,
      dialog: false,
      editedItem: {},
      updating: ""
    }
  },
  created() {
    this.fetch_system_info();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    ...mapState({
        CDN_URL: 'CDN_URL'
    }),
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_SYSTEM_INFO',
      'UPDATE_CLIENT_VERSION',
      'UPDATE_PATCH_VERSION',
      "PATCH_RESOURCE",
      "UPDATE_DESIGN"
    ]),
    get_product_name(seq_product) {
      let product_info = this.product_arranged_map[seq_product];
      return product_info ? product_info.product_name : ""
    },
    makeDate(date) {
      return dateFormat(date, 'yyyy-mm-dd HH:MM');
    },
    update_client_version() {
        this.UPDATE_CLIENT_VERSION(this.patch_version_info)
        .then(() => save_error = "")
        .catch((err) => {
          this.save_error = err;
          console.log(err);
        })
    },
    async patch_resource() {
        this.updating = "리소스 패치중";
        let result = await this.PATCH_RESOURCE();
        console.log(result);
        if(result.error) {
          this.save_error = result.error;
          this.updating = "리소스 패치중";
        }
        else {
          this.updating = "";
          this.fetch_system_info();
        }
    },
    async update_design() {
        let target_arranged_map = _.keyBy(this.target_list, "value")
        console.log(this.db_target);
        if(!confirm(`${target_arranged_map[this.db_target].name} 서버에 디비 패치를 하시겠습니까?`)) { return; }
        this.updating = "DB 업데이트 중";
        let result = await this.UPDATE_DESIGN(this.db_target);
        if(result.error) {
          this.save_error = result.error;
          this.updating = "DB 업데이트 중";
        }
        else {
          this.updating = "";
          this.fetch_system_info();
        }
    },
    fetch_system_info() {
      this.FETCH_SYSTEM_INFO()
      .then((body_data) => {
          if(body_data.error) {
            this.updating = body_data.error;
            return;
          }
          
          this.patch_version_info = body_data.patch_version_info;
          this.client_version_info = body_data.client_version_info;
      });
    },
    editItem (item) {
      this.insert = false;
      this.editedItem = Object.assign({}, item)
      this.image_file = {};
      this.image_url = `${this.CDN_URL}${this.editedItem.image}`;
      this.dialog = true
    },
    deleteItem (item) {
      
    },
    close () {
      this.dialog = false
    },
    createNewBanner() {
      this.editedItem = {}
      this.image_file = {};
      this.image_url = ''
      this.insert = true;
      this.dialog = true;
    },
    save () {
      if(this.image_file && this.image_file.file) this.editedItem.image = `images/${this.patch_info}/${this.image_file.file.name}`
      
      if(this.insert) {
        this.INSERT_NOTICE(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
          
            this.fetch_notice_list();
            this.close();
          })
      }
      else {
        this.UPDATE_NOTICE(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);

            this.fetch_notice_list();
            this.close();
          })
      }
      
      
      
    },
    import_file(file) {
      if(!file) return;
      var reader = new FileReader();
      reader.onload = (e) => {
            var data = e.target.result;
            var workbook = XLSX.read(data, {
            type: 'binary'
          });
          
          workbook.SheetNames.forEach((sheetName) => {
          var json_object = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          this.UPDATE_NOTICE_INFO_LIST({notice_list: json_object})
            .then(() => {
              this.fetch_notice_list();
            });
        })
      };

      reader.onerror = function(ex) { console.log(ex);};

      reader.readAsBinaryString(file);
    },
    export_file() {
        var ws = XLSX.utils.json_to_sheet(this.notice_list);
        let wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws,"notice");
        XLSX.writeFile(wb, 'NOTICE.xlsx');
    },
  }
};
</script>
