<template>
  <v-container>
    <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
    
    <v-layout
      text-center
      wrap
    >
    <v-container>
      <v-alert v-if="isLive" type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
    </v-container>

    <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
    <v-btn v-if="isLive" color="primary" dark class="mb-2" @click="meta_update_live">메타 파일 서버 적용(Live)</v-btn>
    </v-layout>
    <v-layout
      text-center
      wrap
    >
    
    
      <v-data-table
        :headers="headers"
        :items="banner_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            
            <v-toolbar-title>배너</v-toolbar-title>
            
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            
            <v-btn color="primary" dark class="mb-2" @click="createNewBanner">New Item</v-btn>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" >
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled v-model="editedItem.seq_banner" label="seq_banner"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.type"
                        :items="type_list"
                        item-text="text"
                        item-value="value"
                        label="링크 타입"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.link_to"
                        :items="notice_list"
                        item-text="title"
                        item-value="seq_notice"
                        label="링크 공지"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.active_yn"
                        :items="active_yn_list"
                        item-text="text"
                        item-value="value"
                        label="활성화 여부"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.link_to_title"
                        :items="title_list"
                        item-text="name"
                        item-value="seq_title"
                        label="연결 타이틀"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.view_rating"
                        :items="view_rating_list"
                        item-text="text"
                        item-value="value"
                        label="등급"
                        persistent-hint
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.url" label="링크 URL"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <img v-if="image_url" :src="image_url" width="212"/>
                      <v-file-input accept="image/*" label="배너 이미지" @change="onImageChange"></v-file-input>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <img v-if="image_detail_url" :src="image_detail_url" width="212"/>
                      <v-file-input accept="image/*" label="상세 이미지" @change="onImageDetailChange"></v-file-input>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.image_url="{ item }">
          <img v-if="item.image_url" :src="get_image_url(item)" width="250"/>
        </template>
        <template v-slot:item.image_detail="{ item }">
          <img v-if="item.image_detail" :src="get_image_detail_url(item)" width="250"/>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import config from '../../src/config/config';

var crc = require('crc');
const dateFormat = require('dateformat');
const {s3_upload, exportPurgeTxt} = require("../util/fileutil");
export default {
  name: 'Banner',
  data() {
    return {
      updating: "",
      image_url: "",
      image_file: {},
      image_detail_url: "",
      image_detail_file: {},
      title_list: [],
      notice_list: [],
      insert: false,
      banner_list: [],
      dialog: false,
      headers: [
        { text: '배너 일련번호', value: 'seq_banner' },
        { text: '링크 타입', value: 'type' },
        { text: '링크', value: 'link_to' },
        { text: '배너 이미지', value: 'image_url' },
        { text: '활성화 여부', value: 'active_yn' },
        { text: '상세 이미지', value: 'image_detail' },
        { text: '타이틀 연결', value: 'link_to_title' },
        { text: '표시 등급', value: 'view_rating' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      type_list: [
        {text: "NONE", value: "NONE"},
        {text: "LINK_TO_NOTICE", value: "LINK_TO_NOTICE"},
        {text: "LINK_TO_TITLE", value: "LINK_TO_TITLE"},
        {text: "LINK_TO_PACKAGE", value: "LINK_TO_PACKAGE"},
        {text: "LINK_TO_URL", value: "LINK_TO_URL"},
        {text: "SHOW_DETAIL_IMG", value: "SHOW_DETAIL_IMG"},
      ],
      active_yn_list: [
        {text: "활성화", value: "Y"},
        {text: "비활성화", value: "N"},
      ],
      view_rating_list: [
        {text: "공통", value: "COMMON"},
        {text: "전체", value: "ALL"},
        {text: "성인", value: "ADULT"},
      ],
      editedItem: {}
    }
  },
  created() {
    this.fetch_banner_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    ...mapState({
        CDN_URL: 'CDN_URL'
    }),
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_BANNER_UPDATE();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      const result = await this.META_BANNER_UPDATE_LIVE();
      exportPurgeTxt(result.purge_list, `BannerMeta_${this.seq_title}_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = ""
    },
    ...mapActions([
      'FETCH_BANNER_LIST',
      'UPDATE_BANNER',
      'INSERT_BANNER',
      'META_BANNER_UPDATE',
      'META_BANNER_UPDATE_LIVE',
      'OPERATE_SERVER',
    ]),
    onImageChange(upload_file) {
      if(!upload_file) {
        this.image_file = {};
        return;
      }
      
      upload_file.arrayBuffer().then((data) => {
        this.image_file.file = upload_file;
        this.image_file.crc = crc.crc32(data).toString(16);
        this.image_file.thumb = URL.createObjectURL(upload_file);
        this.image_url = URL.createObjectURL(upload_file);
      }) 
    },
    onImageDetailChange(upload_file) {
      if(!upload_file) {
        this.image_detail_file = {};
        return;
      }
      
      upload_file.arrayBuffer().then((data) => {
        this.image_detail_file.file = upload_file;
        this.image_detail_file.crc = crc.crc32(data).toString(16);
        this.image_detail_file.thumb = URL.createObjectURL(upload_file);
        this.image_detail_url = URL.createObjectURL(upload_file);
      }) 
    },
    get_image_url(item) {
      return `${this.CDN_URL}${item.image_url}`;
    },
    get_image_detail_url(item) {
      return `${this.CDN_URL}${item.image_detail}`;
    },
    fetch_banner_list() {
      this.FETCH_BANNER_LIST()
      .then((body_data) => {
        this.banner_list = body_data.banner_list;
        this.title_list = body_data.title_list;
        this.notice_list = body_data.notice_list;
        this.patch_info = body_data.patch_info;
      });
    },
    editItem (item) {
      this.insert = false;
      this.editedItem = Object.assign({}, item)
      this.image_file = {};
      this.image_detail_file = {};
      this.image_url = `${this.CDN_URL}${this.editedItem.image_url}`;
      this.image_detail_url = `${this.CDN_URL}${this.editedItem.image_detail}`;
      this.dialog = true
    },
    deleteItem (item) {
      
    },
    close () {
      this.dialog = false
    },
    createNewBanner() {
      this.image_url = "";
      this.image_detail_url = "";
      this.image_file = {};
      this.image_detail_file = {};
      this.editedItem = {}
      this.insert = true;
      this.dialog = true;
    },
    save () {
      if(this.image_file && this.image_file.file) this.editedItem.image_url = `images/${this.patch_info}/${this.image_file.file.name}`
      if(this.image_detail_file && this.image_detail_file.file) this.editedItem.image_detail = `images/${this.patch_info}/${this.image_detail_file.file.name}`
      
      if(this.insert) {
        this.INSERT_BANNER(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
            if( this.image_detail_file.file ) await s3_upload(this.image_detail_file.file, `images/${this.patch_info}/${this.image_detail_file.file.name}`);
          
            this.fetch_banner_list();
            this.close();
          })
      }
      else {
        this.UPDATE_BANNER(this.editedItem)
          .then(async () => {
            if( this.image_file.file ) await s3_upload(this.image_file.file, `images/${this.patch_info}/${this.image_file.file.name}`);
            if( this.image_detail_file.file ) await s3_upload(this.image_detail_file.file, `images/${this.patch_info}/${this.image_detail_file.file.name}`);

            this.fetch_banner_list();
            this.close();
          })
      }
      
      
      
    },
  }
};
</script>
