<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>

      <v-container v-if="isLive" >
        <v-alert type="info" dense> 퍼지 목록 퍼지 -> 데이터 수정 -> 디자인 적용</v-alert>    
      </v-container>

      <v-container>
        <v-layout align-left>
          
          <v-btn v-if="!isLive" align="left" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>
          <v-btn v-if="isLive" align="left" color="primary" dark class="mb-2" @click="meta_update_live">메타 파일 서버 적용(Live)</v-btn>
          
        </v-layout>
      </v-container>
      
      <v-data-table
        :headers="headers"
        :items="sale_event_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>무료 이벤트</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-btn align="left" color="primary" dark class="mb-2" @click="createSaleEvent">New Item</v-btn>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled v-model="editedItem.seq_event" label="seq_event"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.contents_type"
                        :items="contents_type_list"
                        item-text="text"
                        item-value="value"
                        label="컨텐츠 타입"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.contents_id"
                        :items="get_contents_id(editedItem)"
                        item-text="text"
                        item-value="value"
                        label="컨텐츠 아이디"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.start_chapter" label="시작 챕터"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.end_chapter" label="종료 챕터"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.start_date" label="시작 일자"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.end_date" label="종료 일자"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.sale_type"
                        :items="sale_type_list"
                        item-text="text"
                        item-value="value"
                        label="세일 타입"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-select
                        v-model="editedItem.active_yn"
                        :items="active_yn_list"
                        item-text="text"
                        item-value="value"
                        label="활성화 여부"
                        persistent-hint
                        
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import config from '../../src/config/config';
const dateFormat = require('dateformat');
const {exportPurgeTxt} = require("../util/fileutil");

export default {
  name: 'Banner',
  data() {
    return {
      updating: '',
      insert: false,
      sale_event_list: [],
      dialog: false,
      contents_type_list: [
        {text: "카테고리", value: "CATEGORY"},
        {text: "타이틀", value: "TITLE"}
      ],
      category_list: [],
      title_list: [],
      sale_type_list: [
        {text: "무료", value: "FREE"}
      ],
      active_yn_list: [
        {text: "활성화", value: "Y"},
        {text: "비활성화", value: "N"},
      ],
      headers: [
        { text: '일련번호', value: 'seq_event' },
        { text: '컨텐츠 타입', value: 'contents_type' },
        { text: '컨텐츠 아이디', value: 'contents_id' },
        { text: '시작 챕터', value: 'start_chapter' },
        { text: '종료 챕터', value: 'end_chapter' },
        { text: '시작 일자', value: 'start_date' },
        { text: '종료 일자', value: 'end_date' },
        { text: '세일 타입', value: 'sale_type' },
        { text: '활성화 여부', value: 'active_yn' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      editedItem: {}
    }
  },
  created() {
    this.fetch_event_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_SALE_EVENT_LIST',
      'UPDATE_SALE_EVENT',
      'INSERT_SALE_EVENT',
      'META_SALE_EVENT',
      'OPERATE_SERVER',
      'DELETE_SALE_EVENT'
    ]),
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_SALE_EVENT();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      let result = await this.META_SALE_EVENT_LIVE();
      exportPurgeTxt(result.purge_list, `ProductMeta_${this.seq_title}_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = ""
    },
    
    fetch_event_list() {
      this.FETCH_SALE_EVENT_LIST()
      .then((body_data) => {
        this.sale_event_list = body_data.sale_event_list;

        this.title_list = body_data.title_list.map((item) => {
          return {value: item.seq_title, text: item.name}
        })
        
        this.category_list = body_data.category_list.map((item) => {
          return {value: item.seq_category, text:item.name}
        })
      });
    },
    editItem (item) {
      this.insert = false;
      this.dialog = true
      this.editedItem = Object.assign({}, item)
    },
    deleteItem (item) {
      this.DELETE_SALE_EVENT(item)
        .then(() => {
          this.fetch_event_list();
        })
    },
    get_contents_id(item) {
      if(item.contents_type=="CATEGORY") {
        return this.category_list;
      }
      else if(item.contents_type=="TITLE") {
        return this.title_list;
      }
    },
    close () {
      this.dialog = false
    },
    createSaleEvent() {
      this.editedItem = {}
      this.editedItem.sale_type = "FREE"
      this.insert = true;
      this.dialog = true;
    },
    save () {
      if(this.insert) {
        this.INSERT_SALE_EVENT(this.editedItem)
          .then(() => {
            this.fetch_event_list();
            this.close();
          })
      }
      else {
        this.UPDATE_SALE_EVENT(this.editedItem)
          .then(() => {
            this.fetch_event_list();
            this.close();
          })
      }
      
      
      
    },
  }
};
</script>
