<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
      <v-container>
        <v-alert v-if="isLive" type="info" dense> 데이터 수정 -> 디자인 적용</v-alert>    
      </v-container>
      <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">메타 파일 서버 적용</v-btn>  
      <v-container>

      </v-container>
      <v-data-table
        :headers="headers"
        :items="access_event_list"
        sort-by="seq_event"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>접속 이벤트</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-btn color="primary" dark class="mb-2" @click="createAccessEvent">New Item</v-btn>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" >
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-row>
                  <v-col cols="12" sm="4">
                    <v-container>
                      
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field disabled v-model="editedItem.seq_event" label="이벤트 일련번호"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.memo" label="메모"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.start_date" label="시작일시"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field v-model="editedItem.end_date" label="종료일시"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12">
                          <v-text-field v-model="editedItem.title" label="제목"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12">
                          <v-textarea v-model="editedItem.contents" label="내용"></v-textarea>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-select
                            v-model="editedItem.active_yn"
                            :items="active_yn_list"
                            item-text="text"
                            item-value="value"
                            label="활성화 여부"
                            persistent-hint
                            
                          ></v-select>
                        </v-col>
                      </v-row>
                    </v-container>
                </v-col>
                <v-col cols="12" sm="8">
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6">
                        <v-select
                            v-model="add_item.item_category"
                            :items="item_category_list"
                            item-text="text"
                            item-value="value"
                            label="보상 타입"
                            persistent-hint
                            
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="6">
                        <v-select
                            v-model="add_item.item_id"
                            :items="getItemId()"
                            item-text="text"
                            item-value="value"
                            label="아이템 아이디"
                            persistent-hint
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="4">
                        <v-text-field v-model="add_item.item_qny" label="아이템 개수"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="4">
                        <v-select
                            v-model="add_item.seq_title"
                            :items="title_list"
                            item-text="name"
                            item-value="seq_title"
                            label="타이틀 리스트"
                            persistent-hint
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="4">
                        <v-btn @click="addItem">추가</v-btn>
                      </v-col>
                      </v-row>
                    <v-divider></v-divider>
                    <div v-for="(item, index) in itemList" :key="index">
                      {{item.item_category}}
                      {{item.item_id}}
                      X {{item.item_qny}}
                      {{get_title_name(item.seq_title)}}
                      <v-btn @click="deleteItem(item)">삭제</v-btn>
                    </div>
                  </v-container>
                </v-col>
              </v-row>
                
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteAccessEventItem(item)"
          >
            delete
          </v-icon>
        </template>
        <template v-slot:item.item_list="{item}">
          <div v-for="(product_item, index) in access_event_arranged_map[item.seq_event]" :key="index">
            {{product_item.item_category}}
            {{product_item.item_id}}
            X {{product_item.item_qny}}
            {{get_title_name(product_item.seq_title)}}
          </div>
        </template>
        
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
import config from '../../src/config/config';

export default {
  name: 'Banner',
  data() {
    return {
      updating: "",
      add_item: {},
      insert: false,
      server_type: "",
      access_event_list: [],
      access_event_item: [],
      access_event_arranged_map: {},
      dialog: false,
      headers: [
        { text: '배너 일련번호', value: 'seq_event' },
        { text: '메모', value: 'memo' },
        { text: '시작일시', value: 'start_date' },
        { text: '종료일시', value: 'end_date' },
        { text: '제목', value: 'title' },
        { text: '내용', value: 'contents' },
        { text: '보상 아이템', value: 'item_list' },
        { text: '활성화 여부', value: 'active_yn' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      active_yn_list: [
        {text: "활성화", value: "Y"},
        {text: "비활성화", value: "N"},
      ],
      item_category_list: [
        {text: "재화", value: "GOOD"},
        {text: "티켓", value: "TICKET"},
      ],
      good_item_list: [
        {text: "젬", value: "GEM"},
        {text: "무료젬", value: "FREE_GEM"},
        {text: "에너지", value: "ENERGY"},
      ],
      ticket_item_list: [
        {text: "소장권", value: "POSSESSION"},
        {text: "대여권", value: "RENTAL"},
      ],
      editedItem: {},
      itemList: [],
      title_list: [],
      title_arranged_map: null
    }
  },
  created() {
    this.server_type = this.$route.params.server_type;
    console.log(this.server_type);
    this.fetch_access_event_list();
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    isLive(){
      return config.isLive;
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
    
  },
  methods: {
    ...mapActions([
      'FETCH_ACCESS_EVENT_LIST',
      'UPDATE_ACCESS_EVENT',
      'INSERT_ACCESS_EVENT',
      'DELETE_ACCESS_EVENT',
      'OPERATE_SERVER'
    ]),
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    get_title_name(seq_title) {
      if(seq_title) {
        return this.title_arranged_map[seq_title].name;
      }
    },
    addItem() {
      this.itemList.push(Object.assign({}, this.add_item));
      this.add_item = {}
    },
    getItemId() {
      if(this.add_item.item_category == "TICKET") {
        return this.ticket_item_list;
      }
      else if(this.add_item.item_category == "GOOD") {
        return this.good_item_list;
      }
    },
    createAccessEvent() {
      this.insert = true;
      this.dialog = true;
      this.editedItem = {}
    },
    fetch_access_event_list() {
      this.FETCH_ACCESS_EVENT_LIST(this.server_type)
      .then((body_data) => {
        this.access_event_list = body_data.access_event_list;
        this.access_event_item = body_data.access_event_item;

        this.access_event_arranged_map = {};
        this.access_event_item.forEach((item) => {
          let seq_item_list = this.access_event_arranged_map[item.seq_event];
          if(seq_item_list) {
            seq_item_list.push(item);
          }
          else {
            this.access_event_arranged_map[item.seq_event] = []
            this.access_event_arranged_map[item.seq_event].push(item);
          }
        });

        this.title_list = body_data.title_list;
        this.title_arranged_map = _.keyBy(this.title_list, "seq_title");
      });
    },
    editItem (item) {
      this.insert = false;
      this.dialog = true
      this.editedItem = Object.assign({}, item)
      this.itemList = this.access_event_arranged_map[item.seq_event];
      console.log(this.itemList);
      if(!this.itemList) this.itemList = [];
    },
    deleteItem (item) {
      const index = this.itemList.indexOf(item)
      this.itemList.splice(index, 1)
    },
    deleteAccessEventItem (item) {
      if(!confirm('해당 이벤트를 삭제 하시겠습니까?')) return;

      this.DELETE_ACCESS_EVENT(item)
        .then(() => {
          this.fetch_access_event_list();
        })
      
    },
    close () {
      this.dialog = false
    },
    save () {
      if(this.insert) {
        this.INSERT_ACCESS_EVENT({access_event_info: this.editedItem, access_event_item: this.itemList, server_type: this.server_type})
          .then(() => {
            this.fetch_access_event_list();
            this.close();
          })
      }
      else {
        console.log(this.editedItem);
        this.UPDATE_ACCESS_EVENT({access_event_info: this.editedItem, access_event_item: this.itemList, server_type: this.server_type})
          .then(() => {
            this.fetch_access_event_list();
            this.close();
          })
      }
    },
  }
};
</script>
