<template>
  <v-container>
    <v-layout text-center wrap>
      <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
    </v-layout>
    <v-toolbar flat color="white">
      <v-toolbar-title>챕터</v-toolbar-title>
      <v-divider
        class="mx-4"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-btn color="primary" dark class="mb-2" @click="onCreate">New Item</v-btn>
    <v-dialog v-model="dialog" >
      <v-card>
        <v-card-title>
          <span class="headline"></span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
            <v-row>
              <v-text-field label="그룹아이디" disabled v-model="editedItem.reward_group_id"></v-text-field>
              <v-text-field label="그룹 이름" v-model="editedItem.reward_group_name"></v-text-field>
            </v-row>
            <v-divider></v-divider>
            
            <v-row v-for="(item, index) in editedItem.item_list" :key="index" no-gutters>
              <v-col>
              <v-select
                v-model="item.reward_type"
                :items="reward_type_list"
                item-text="text"
                item-value="value"
                label="보상 타입"
                persistent-hint
                
              ></v-select>
              </v-col>
              <v-col>
              <v-select
                v-model="item.item_category"
                :items="item_category_list"
                item-text="text"
                item-value="value"
                label="카테고리 타입"
                persistent-hint
                
                @change="onChange"
              ></v-select>
              </v-col>
              <v-col>
              <v-select
                v-model="item.item_id"
                :items="get_item_id_list(item.item_category)"
                item-text="text"
                item-value="value"
                label="아이템 타입"
                persistent-hint
                
                @change="onChange"
              ></v-select>
              </v-col>
              
              <v-col>
              <v-text-field label="아이템 개수" v-model="item.item_qny"></v-text-field>
              </v-col>
              <v-col>
              <v-select
                :disabled="is_title_list_disabled(item)"
                v-model="item.seq_title"
                :items="title_list"
                item-text="name"
                item-value="seq_title"
                label="타이틀 번호"
                persistent-hint
                
              ></v-select>
              </v-col>
              <v-col>
              <v-btn @click="deleteItem(item)">삭제</v-btn>
              </v-col>
            </v-row>
            <v-divider></v-divider>
            신규 추가
            <v-row no-gutters>
              <v-col>
              <v-select
                v-model="new_item.reward_type"
                :items="reward_type_list"
                item-text="text"
                item-value="value"
                label="보상 타입"
                persistent-hint
                
              ></v-select>
              </v-col>
              <v-col>
              <v-select
                v-model="new_item.item_category"
                :items="item_category_list"
                item-text="text"
                item-value="value"
                label="카테고리 타입"
                persistent-hint
                
              ></v-select>
              </v-col>
              <v-col>
              <v-select
                v-model="new_item.item_id"
                :items="get_item_id_list(new_item.item_category)"
                item-text="text"
                item-value="value"
                label="아이템 타입 타입"
                persistent-hint
                
              ></v-select>
              </v-col>
              
              <v-col>
              <v-text-field label="아이템 개수" v-model="new_item.item_qny"></v-text-field>
              </v-col>
              <v-col>
              <v-select
                :disabled="is_title_list_disabled_new_item()"
                v-model="new_item.seq_title"
                :items="title_list"
                item-text="name"
                item-value="seq_title"
                label="타이틀 번호"
                persistent-hint
                
              ></v-select>
              </v-col>
              <v-col>
                <v-btn @click="addItem">추가</v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save">Save</v-btn>
        </v-card-actions>
      </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
        :headers="headers"
        :items="reward_group"
        sort-by="calories"
        class="elevation-1"
      >

      <template v-slot:item.item_list="{ item }">
        <div v-for="(reward_item, index) in item.item_list" :key="index">
          {{get_item_reward_type(reward_item.reward_type)}} 
          {{get_item_category(reward_item.item_category)}} 
          {{get_item_id(reward_item.item_id)}}
          x {{reward_item.item_qny}}
          {{get_title_name(reward_item.seq_title)}}
        </div>
      </template>
      <template v-slot:item.action="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>
      </template>
    </v-data-table>
  </v-container>
</template>

<script src="xlsx.full.min.js"></script>
<script src="js/jhxlsx.js"></script>

<script>
import { mapActions } from 'vuex';
import XLSX from 'xlsx';
export default {
  name: 'ChapterInfo',
  data() {
    return {
      insert: false,
      new_item: {},
      title_list: [],
      dialog: false,
      save_error: '',
      editedItem: {},
      updating: '',
      title_arranged_map: null,
      reward_group: [],
      reward_group_list: [],
      reward_group_item_list: [],
      headers: [
        { text: '그룹 아이디', value: 'reward_group_id' },
        { text: '그룹 이름', value: 'reward_group_name', sortable: false },
        { text: '아이템 리스트', value: 'item_list', sortable: false },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      reward_type_list: [
        {text: "일반", value: "NORMAL"},
        {text: "보너스", value: "BONUS"},
      ],
      item_category_list: [
        {text: "티켓", value: "TICKET"},
        {text: "재화", value: "GOOD"},
      ],
      good_list: [
        {text: "젬", value: "GEM"},
        {text: "에너지", value: "ENERGY"},
        {text: "무료 잼", value: "FREE_GEM"},
      ],
      ticket_list: [
        {text: "소장권", value: "POSSESSION"},
        {text: "대여권", value: "RENTAL"},
      ],
    }
  },
  
  created() {
    this.fetch_reward_group();
  },
  computed: {
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_REWARD_GROUP',
      'UPDATE_REWARD_GROUP',
      'INSERT_REWARD_GROUP',
      'META_REWARD_UPDATE',
      'OPERATE_SERVER'
    ]),
    onChange() {
      console.log("onChange");
      this.$forceUpdate();
    },
    deleteItem(item) {
      const index = this.editedItem.item_list.indexOf(item)
      console.log(index)
      this.editedItem.item_list.splice(index, 1)
      this.$forceUpdate();
    },
    is_title_list_disabled_new_item() {
      if(this.new_item.item_category == "TICKET") {
        return false;
      }
      this.new_item.seq_title = -1
      return true;
    },
    is_title_list_disabled(item) {
      if(item.item_category == "TICKET") {
        return false
      }
      else {
        item.seq_title = 0
        return true;
      }
    },
    addItem() {
      console.log("addItem: new_item", this.new_item);
      console.log("addItem: editedItem", this.editedItem);
      this.editedItem.item_list.push(Object.assign({},  this.new_item));
      this.new_item = {}
    },
    onCreate() {
      this.editedItem = {};
      this.editedItem.item_list = [];
      this.new_item = {}
      this.dialog = true;
      this.insert = true;
    },
    async meta_update() {
      this.updating = "메타 파일 업데이트 중 잠시만 기다려 주세요"
      await this.META_QURATION_UPDATE();
      this.updating = "서버 중지중...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = "서버 재시작...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    get_item_reward_type(reward_type) {
      if(reward_type=="NORMAL") { return "일반" }
      else if(reward_type=="BONUS") { return "보너스" }
    },
    get_item_category(item_category) {
      if(item_category=="GOOD") { return "재화" }
      else if(item_category=="TICKET") { return "소장/대여권" }
    },
    get_item_id(item_id) {
      if(item_id=="GEM") { return "젬" }
      else if(item_id=="ENERGY") { return "에너지" }
      else if(item_id=="POSSESSION") { return "소장권" }
      else if(item_id=="RENTAL") { return "대여권" }
    },
    get_item_id_list(item_category) {
      if(item_category == "GOOD") {
        return this.good_list
      }
      else if(item_category == "TICKET") {
        return this.ticket_list
      }
      
    },
    get_title_name(seq_title) {
      if(seq_title) return this.title_arranged_map[parseInt(seq_title)].name;
    },
    editItem (item) {
      this.insert = false;
      this.editedItem = Object.assign({}, item)
      this.editedItem.item_list = item.item_list ? item.item_list.map(a => Object.assign({}, a)) : []

      this.dialog = true;
    },
    fetch_reward_group() {
      this.$nextTick(() => {
        this.FETCH_REWARD_GROUP()
        .then((body_data) => {
          this.reward_group = [];
          this.reward_group_list = body_data.reward_group_list;
          this.reward_group_item_list = body_data.reward_group_item_list;
          this.title_list = body_data.title_list;
          
          this.title_arranged_map =  _.keyBy(this.title_list, "seq_title")
          console.log(this.title_arranged_map);
          let reward_item_arranged_object = {};
          this.reward_group_item_list.forEach((item) => {
            let reward_gorup_object = reward_item_arranged_object[item.reward_group_id];
            if(!reward_gorup_object) {
              reward_item_arranged_object[item.reward_group_id] = [];
              reward_item_arranged_object[item.reward_group_id].push(item);
            }
            else {
              reward_gorup_object.push(item);
            }
          });

          this.reward_group_list.forEach((item) => {
             let reward_object = Object.assign({}, item);
             reward_object.item_list = reward_item_arranged_object[item.reward_group_id];
             this.reward_group.push(reward_object);
          })
        })
      })
    },
    save() {
      let reward_group = {
        reward_group_id: this.editedItem.reward_group_id,
        reward_group_name: this.editedItem.reward_group_name
      }

      let reward_group_item = this.editedItem.item_list;

      if(this.insert) {
        
        this.INSERT_REWARD_GROUP({reward_group, reward_group_item})
        .then(() => {
          this.fetch_reward_group();
          this.close()
        })
      }
      else {
        this.UPDATE_REWARD_GROUP({reward_group, reward_group_item})
        .then(() => {
          this.fetch_reward_group();
          this.close()
        })
      }
      
      
      
    },
    close () {
      this.dialog = false
    },
  }
};
</script>
