<template>
  <v-container>
    <v-layout
      text-center
      wrap
    >
      <v-data-table
        :headers="headers"
        :items="character_list"
        sort-by="calories"
        class="elevation-1"
      >
      <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>캐릭터</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="headline"></span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-alert v-if="save_error" type="error">{{save_error}}</v-alert>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.seq_character" label="일련번호"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.first_name" label="이름"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.last_name" label="성"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.nick_name" label="별명"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.change_name_type" label="이름 변경 타입"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        
        <template v-slot:no-data>
          <v-btn color="primary">Reset</v-btn>
        </template>
      </v-data-table>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'SideChapterInfo',
  data() {
    return {
      save_error: '',
      character_list: [],
      seq_title: 0,
      dialog: false,
      headers: [
        { text: '일련번호', value: 'seq_character' },
        { text: '이름', value: 'first_name' },
        { text: '성', value: 'last_name' },
        { text: '별명', value: 'nick_name' },
        { text: '이름 변경 타입', value: 'change_name_type' },
      ],
      editedItem: {}
    }
  },
  created() {
    this.seq_title = this.$route.params.seq_title
    console.log(this.seq_title);
    this.FETCH_CHARACTER_LIST(this.seq_title)
      .then((body_data) => {this.character_list = body_data.character_list});
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    ...mapActions([
      'FETCH_CHARACTER_LIST',
      'UPDATE_CHARACTER'
    ]),
    editItem (item) {
      this.editedIndex = this.character_list.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.character_list.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.character_list.splice(index, 1)
    },
    close () {
      this.dialog = false
    },
    save () {
      if(!this.check_null_value(this.editedItem.seq_chapter, this.$refs.seq_chapter, "회차를 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.preview_start_date, this.$refs.preview_start_date, "미리 보기 시작을 입력해주세요")) return;
      if(!this.check_null_value(this.editedItem.publish_start_date, this.$refs.publish_start_date, "공개 시작을 입력해주세요")) return;

    },
  }
};
</script>
